<template>
  <div class="container">
    <el-image :src="require('@/assets/images/wechart.png')" fit="cover"></el-image>
    <p style="font-size: 16px">请打开手机微信扫一扫二维码</p>
  </div>
</template>

<script>
export default {
  name: 'BindWeChart',
  data() {
    var checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('手机号不能为空'))
      } else {
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
        if (reg.test(value)) {
          callback()
        } else {
          return callback(new Error('请输入正确的手机号'))
        }
      }
    }
    return {
      formData: {
        phone: '',
        verifycode: '',
        password: ''
      },
      formDataRules: {
        phone: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: checkPhone, trigger: ['blur', 'change'] }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 11, message: '长度在 6 到 11 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {}
}
</script>

<style lang="less" scoped>
.container
{
  text-align: center;
}
</style>
