<template>
    <div>
        <div class="dialog_title">牪牪牛</div>
        <el-divider>使用第三方账户登录</el-divider>
        <el-row :gutter="25" type="flex" justify="center">
        <el-col :span="8" class="col-image">
            <el-image :src="require('../../assets/icon/wx.png')" fit="cover"></el-image>
        </el-col>
        <el-col :span="8" class="col-image">
            <el-image :src="require('../../assets/icon/qq.png')" fit="cover"></el-image>
        </el-col>
        </el-row>
        <!-- 手机号 -->
        <el-divider>使用手机号登录</el-divider>
        <el-form ref="loginFormRef" :model="formData" :rules="formDataRules"
            label-width="0px" class="login_form">
        <el-form-item prop="phone">
            <el-input v-model="formData.phone"
                prefix-icon="el-icon-mobile-phone" placeholder="手机号">
            </el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
            <el-input  v-model="formData.password" type="password"
                prefix-icon="el-icon-lock" show-password placeholder="密码">
            </el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" class="login_btn" @click="submitForm()">
                登录
            </el-button>
        </el-form-item>
        </el-form>
        <el-row type="flex" justify="space-between" class="jump_link">
        <el-col :span="12"><el-link type="primary" @click="toRetrievePwd()">忘记密码>>></el-link></el-col>
        <el-col :span="8"><el-link type="primary" @click="toRegister()">去注册>>></el-link></el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    data () {
        var checkPhone = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('手机号不能为空'))
            } else {
                const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
                if (reg.test(value)) {
                    callback()
                } else {
                    return callback(new Error('请输入正确的手机号'))
                }
            }
        }
        return {
            formData: {
                phone: '',
                password: ''
            },
            formDataRules: {
                phone: [
                    { required: true, message: '请输入手机号', trigger: 'blur' },
                    { validator: checkPhone, trigger: ['blur', 'change'] }
                ],
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { min: 6, max: 11, message: '长度在 6 到 11 个字符', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        toRegister () {
            this.$emit('getMessage', '85px')
            this.$router.push('/register')
        },
        toRetrievePwd () {
            this.$emit('getMessage', '185px')
            this.$router.push('/retrievePwd')
        },
        submitForm () {
            this.$refs.loginFormRef.validate(async valid => {
                if (!valid) {
                    return
                }
                const { data: res } = await this.$http.post('login/uandp', this.formData)
                console.log(res)
                // alert(JSON.stringify(res.data))
                if (res.meta.status !== 200) {
                    return this.$message.error('登录失败！')
                }
                // this.$message({message:'登录成功！',type:'info'});
                this.$message.success('登录成功！')
                // window.sessionStorage.setItem('token', res.data.token)
                window.sessionStorage.setItem('userName',res.data.username)
                window.sessionStorage.setItem('userId',res.data.id)
                this.$router.push({
                    path: '/sssmarket'
                })
            })
        }
    }
}
</script>

