<template>
  <div class="container">
    <el-row type="flex" justify="space-around">
      <el-col :span="11" >
        <span style="font-size: 25px;color: #898989;padding-left:5px">饲料</span>
        <span style="color: #898989"> (886033)</span>
        <el-row class="detail">
          <el-col :span="5">
            <p style="font-size: 29px; color: #FF0000">+6.95%</p>
            <p style="font-size: 13px;">
              <span style="color: #898989; margin-right: 10px">成交额</span>
              <span>1.54亿</span>
            </p>
          </el-col>
          <el-col :offset="4" :span="2">
            <p style="font-size: 29px; color: #FF0000">3</p>
            <p style="font-size: 13px;color: #898989">
              上涨
            </p>
          </el-col>
          <el-col :offset="1" :span="2">
            <p style="font-size: 29px; color: #66bf8a">3</p>
            <p style="font-size: 13px;color: #898989">
              下跌
            </p>
          </el-col>
          <el-col :offset="1" :span="2">
            <p style="font-size: 29px">3</p>
            <p style="font-size: 13px;color: #898989">
              平盘
            </p>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="11" style="text-align: end;">
        <el-link type="danger" href="#/block">返回板块列表&gt;</el-link>
      </el-col>
    </el-row>
    <div class="tableGutter">
      <el-table :data="tableData" border height="435"
        :cell-style="()=>{return 'text-align: center'}"
        :header-cell-style="()=>{return 'background:#fef1f0;text-align: center'}">
        <el-table-column type="index" label="#" width="39px" fixed="left">
        </el-table-column>
        <el-table-column prop="name" label="名称" fixed="left">
        </el-table-column>
        <el-table-column prop="last" label="最新">
        </el-table-column>
        <el-table-column prop="uprange" label="涨幅">
        </el-table-column>
        <el-table-column prop="charge" label="涨跌">
        </el-table-column>
        <el-table-column prop="prevclose" label="昨收">
        </el-table-column>
        <el-table-column prop="volume" label="成交量">
        </el-table-column>
        <el-table-column prop="turnover" label="成交额">
        </el-table-column>
        <el-table-column prop="max" label="最高">
        </el-table-column>
        <el-table-column prop="min" label="最低">
        </el-table-column>
      </el-table>
      <el-pagination :current-page="this.queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10, 20, 100, 200]"
        :page-size="queryInfo.pagesize"
        :total="this.total" background
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  created () {
    this.getTableData()
  },
  data () {
    return {
      queryInfo: {
        blockname: 'xxx',
        pagenum: 1,
        pagesize: 3
      },
      total: 10,
      tableData: [
        {
          name: 'NULL',
          last: 0,
          uprange: 0,
          charge: 0,
          prevclose: 0,
          volume: 0,
          turnover: 0,
          max: 0,
          min: 0,
          amplitude: 0,
          accelerate: 0
        }
      ]
    }
  },
  methods: {
    async getTableData () {
      const { data: res } = await this.$http.get('market/block/detail', { params: this.queryInfo })
      if (res.meta.status !== 200) {
          return this.$message.error('数据请求失败')
      }
      // this.tableData = res.data.detail
      // this.total = res.data.totalpage
      // console.log('请求参数')
      // console.log(this.queryInfo)
      // console.log('数据')
      console.log(res)
      // return this.$message.success('数据请求成功')
    },
    handleSizeChange (val) {
        this.queryInfo.pagesize = val
        this.getTableData()
        // console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
        this.queryInfo.pagenum = val
        this.getTableData()
        // console.log(`当前页: ${val}`)
    }
  }
}
</script>

<style lang="less" scoped>
.container
{
  margin-top: 5px;
}
.detail
{
  position: relative;
  top:-12px
}
.tableGutter
{
  margin: 0 25px 0 25px;
}
.el-col p
{
  line-height: 0px;
  text-align: center;
}
.el-pagination
{
  margin: 10px 0 10px 0;
}
::v-deep
{
  .el-pagination.is-background .el-pager li:not(.disabled).active
  {
    background-color: #f14747;   // 进行修改选中项背景和字体
    color: #EEEEEE;
  }
  .el-pagination.is-background .el-pager li:not(.active):hover
  {
    color:#ff4040;
  }
}
</style>
