<template>
  <div class="container">
    <ul ref="scroll" class="infinite-list" v-infinite-scroll="getInfos" style="overflow:auto;" infinite-scroll-disabled="disabled">
      <li v-for="i in count" :key="i" class="infinite-list-item">
        <el-container>
          <el-main>
            <el-link :underline="false" @click="toDetail(showData[i-1].id)"> {{showData[i-1].stockName}}:{{showData[i-1].head}}</el-link>
          </el-main>
          <el-footer height="auto">
            <el-row class="detailInfo">
              <el-col :span="12">{{showData[i-1].author}} {{showData[i-1].date}}</el-col>
              <el-col :span="12" style="text-align: end;">#{{showData[i-1].tag}} {{showData[i-1].stockName}}</el-col>
            </el-row>
          </el-footer>
        </el-container>
      </li>
      <div class="tips">
        <p v-if="loading">加载中...</p>
        <p v-if="noMore">没有更多了</p>
      </div>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'SSInfo',
  props: {
    // itemvisible: Boolean
  },
  computed: {
    noMore () {
      return this.count >= this.totalInfoCount
    },
    disabled () {
      return this.loading || this.noMore 
    }
  },
  created () {
    this.back = this.$route.query.back
    this.backId = window.sessionStorage.getItem('infoCount')
    this.back ? '' : this.updateCount++
  },
  updated () {
    if(this.updateCount < 2) {
      this.$nextTick(() => {
        this.$refs.scroll.scrollTop = window.sessionStorage.getItem('scrollTop')
      })
    }
  },
  data () {
    return {
      count: 0,
      updateCount: 0,
      totalInfoCount: 10,
      loading: false,
      loadingTimer: 0,
      back: false,
      queryInfo: {
        pagenum: 1,
        pagesize: 10
      },
      showData: [
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'1'
        // },{
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'2'
        // },
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'3'
        // },{
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'4'
        // },{
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'5'
        // },
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'6'
        // },
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'7'
        // },
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'8'
        // },
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'9'
        // },
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'10'
        // }
      ]
    }
  },
  methods: {
    getInfos () {
      if (this.back) {
        // console.log(this.showData)
        // console.log(this.count)
        this.backId > 10 ? this.backId % 2 > 0 ? this.queryInfo.pagesize = this.backId - 1 : this.queryInfo.pagesize = this.backId - 0 : ''
        this.back = !this.back
      }
      this.loading = true
      setTimeout(async () => {
        const { data: res } = await this.$http.get('infos/simple', { params: this.queryInfo })
        if (res.meta.status !== 200) {
            return this.$message.error('数据请求失败')
        }
        this.showData.push(...res.data.detail) 
        this.count += res.data.detail.length
        this.totalInfoCount = res.data.totalpage
        this.queryInfo.pagenum++
        // this.total = res.data.totalpage
        console.log(res)
        // return this.$message.success('数据请求成功')
        this.loading = false
      }, this.loadingTimer)
      this.updateCount++
    },
    toDetail (id) {
      console.log(this.$refs.scroll.scrollTop)
      window.sessionStorage.setItem('scrollTop',this.$refs.scroll.scrollTop)
      window.sessionStorage.setItem('infoCount',this.count)
      this.$router.push({
        path: '/detailInfo',
        query: {
          infoid: id
        }
      })
    }
  },
  watch: {
    count (v) {
      if(this.loadingTimer === 0 && v >= 10) {
        this.queryInfo.pagenum = this.count / 2 + 1
        this.queryInfo.pagesize = 2
        this.loadingTimer = 1000
      }
      if (v > this.totalInfoCount - this.queryInfo.pagesize) {
        this.queryInfo.pagesize = this.totalInfoCount - v
        this.queryInfo.pagenum = this.totalInfoCount / this.queryInfo.pagesize + 1
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-main
{
  padding: 0px;
}
.el-footer
{
  padding: 0px;
}
.el-link.el-link--default:hover
{
  color: #ff0000;
}
.infinite-list {
  height: 585px;
  padding: 0;
  margin: 3px 0 0 0;
  list-style: none;
}
.infinite-list .infinite-list-item {
  display: flex;
  align-items: center;
  // justify-content: center;
  height: 60px;
  background: #f9f9f9;
  margin: 5px;
  color: #6d6d6d;
}
.detailInfo
{
  font-size: 13px;
  color: #b1b1b1;
}
.container
{
  padding: 5px;
}
.tips
{
  text-align: center;
  color: #6d6d6d;
  line-height: 3px;
}
</style>
