<template>
  <div class="container-router">
    <!-- <AdvancedQuery v-if="componentsVisible" @getMessage="toResults"></AdvancedQuery> -->
    <SimpleSearch v-if="componentsVisible" @getMessage="toQuery" 
      :visible="!componentsVisible"></SimpleSearch>
    <QueryResults v-else-if="!componentsVisible" @getMessage="toQuery" 
      :visible="!componentsVisible" :carryData="this.carryData"></QueryResults>
  </div>
</template>

<script>
import AdvancedQuery from './AdvancedQuery.vue'
import QueryResults from './QueryResults.vue'
import SimpleSearch from './SimpleSearch.vue'
export default {
  components:{ AdvancedQuery, QueryResults, SimpleSearch },
  props: ['carryData'],
  created () {
    this.componentsVisible = !this.$route.query.results
  },
  data() {
    return {
      componentsVisible: true,
      simpleSearchVisible: false
    }
  },
  methods: {
    toResults () {
      this.$router.push({
        path: '/pickStocks',
        query: {
          results: true
        }
      })
    },
    toQuery () {
      this.$router.push('/pickStocks')
      console.log(1)
    },
    toSimpleSearch () {
      this.simpleSearchVisible = !this.simpleSearchVisible
    }
  },
  watch: {
    simpleSearchVisible () {
      this.$emit('toSimpleSearch',true)
    },
    $route () {
      this.componentsVisible = !this.$route.query.results
    }
  }
}
</script>

<style lang="less" scoped>
.container-router {
  width: 100%;
  height: 100%;
  font-size: 16px;
  overflow: hidden scroll;
}
</style>
