<template>
  <el-card v-if="isCardShow" class="card-container">
    <div slot="header" class="clearfix">
      <span class="card-title">公告栏</span>
      <div class="button-group" v-if="!isEdit">
        <el-button v-if="isAdmin" @click="handleEdit" class="card-icon" type="success" plain icon="el-icon-edit" circle size="mini"></el-button>
        <el-button @click="handleClose" class="card-icon" type="danger" plain icon="el-icon-close" circle size="mini"></el-button>
      </div>
      <div class="button-group" v-else>
        <el-button @click="handleCheck" class="card-icon" type="success" icon="el-icon-check" circle size="mini"></el-button>
        <el-button @click="handleCancel" class="card-icon" type="danger" icon="el-icon-close" circle size="mini"></el-button>
      </div>
    </div>
    <div v-if="!isEdit" class="text">{{ text }}</div>
    <el-input v-else type="textarea" :rows="3" placeholder="请输入内容" v-model="textarea"></el-input>
  </el-card>
</template>

<script>
export default {
  data () {
    return {
      text: '',
      textarea: '',
      isEdit: false,
      isCardShow: true,
    }
  },
  methods: {
    handleEdit () {
      this.isEdit = true;
      this.textarea = this.text;
    },
    handleClose () {
      this.isCardShow = false;
    },
    handleCheck () {
      this.isEdit = false;
      this.text = this.textarea;
    },
    handleCancel () {
      this.isEdit = false;
    }
  },
  computed: {
    isAdmin () {
      return true;
    }
  }
}
</script>

<style lang="less" scoped>
  .card-container {
    width: 180px;
    min-height: 180px;
    max-height: 268px;
  }
  .clearfix {
    display: flex;
    height: 21px;
    justify-content: space-between;
    align-items: center;
  }
  .text {
    white-space: pre-wrap;
    max-height: 168px;
    overflow-y: auto;
  }
</style>