<template>
  <div class="container">
    <el-divider>判断项目</el-divider>
    <el-row>
      <el-col :offset="2" :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('开')">开</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('高')">高</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('低')">低</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('收')">收</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('量')">量</el-button></el-col>
      <el-col :offset="2" :span="4">
        <el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('短线价')">○短线价</el-button>
      </el-col>
      <el-col :span="4">
        <el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('中线价')">☉中线价</el-button>
      </el-col>
      <el-col :span="4">
        <el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('长线价')">◎长线价</el-button>
      </el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('主力量')">主力量</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('量能比')">量能比</el-button></el-col>
      <el-col :offset="2" :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('原获利比')">原获利比</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('获利比')">获利比</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('涨跌幅')">涨跌幅</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('涨跌')">涨跌</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('数值')">数值</el-button></el-col>
      <el-col :offset="2" :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('MACD金叉')">MACD金叉</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('MACD死叉')">MACD死叉</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('KDJ金叉')">KDJ金叉</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('KDJ死叉')">KDJ死叉</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('短线买')">短线买</el-button></el-col>
      <el-col :offset="2" :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('中线买')">中线买</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('中线卖')">中线卖</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('长线买')">长线买</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('长线卖')">长线卖</el-button></el-col>
      <el-col :span="4"><el-button plain type="danger" :disabled="buttonSwitch != 0" @click="queryChanged('短线卖')">短线卖</el-button></el-col>
    </el-row>
    <el-divider>逻辑运算符</el-divider>
    <el-row>
      <el-col :offset="3" :span="3"><el-button type="danger">一键牛股</el-button></el-col>
      <el-col :span="3"><el-button plain type="warning" :disabled="buttonSwitch != 1" @click="queryChanged(' < ')">&lt;</el-button></el-col>
      <el-col :span="3"><el-button plain type="warning" :disabled="buttonSwitch != 1" @click="queryChanged(' == ')">==</el-button></el-col>
      <el-col :span="3"><el-button plain type="warning" :disabled="buttonSwitch != 1" @click="queryChanged(' > ')">&gt;</el-button></el-col>
      <el-col :span="3"><el-button plain type="warning" :disabled="buttonSwitch != 3" @click="queryChanged(' && ')">&amp;&amp;</el-button></el-col>
      <el-col :span="3"><el-button plain type="warning" :disabled="buttonSwitch != 3" @click="queryChanged(' || ')">||</el-button></el-col>
    </el-row>
    <el-row>
      <el-col :span="16">
        <el-form :model="formData">
          <el-form-item>
            <el-input type="textarea" :rows="7" placeholder="请输入内容" v-model="formData.textarea"></el-input>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span="8">
        <el-row class="numbers">
          <el-col :span="8"><el-button plain type="warning" :disabled="buttonSwitch < 2" @click="queryNumChanged('1')">1</el-button></el-col>
          <el-col :span="8"><el-button plain type="warning" :disabled="buttonSwitch < 2" @click="queryNumChanged('2')">2</el-button></el-col>
          <el-col :span="8"><el-button plain type="warning" :disabled="buttonSwitch < 2" @click="queryNumChanged('3')">3</el-button></el-col>
          <el-col :span="8"><el-button plain type="warning" :disabled="buttonSwitch < 2" @click="queryNumChanged('4')">4</el-button></el-col>
          <el-col :span="8"><el-button plain type="warning" :disabled="buttonSwitch < 2" @click="queryNumChanged('5')">5</el-button></el-col>
          <el-col :span="8"><el-button plain type="warning" :disabled="buttonSwitch < 2" @click="queryNumChanged('6')">6</el-button></el-col>
          <el-col :span="8"><el-button plain type="warning" :disabled="buttonSwitch < 2" @click="queryNumChanged('7')">7</el-button></el-col>
          <el-col :span="8"><el-button plain type="warning" :disabled="buttonSwitch < 2" @click="queryNumChanged('8')">8</el-button></el-col>
          <el-col :span="8"><el-button plain type="warning" :disabled="buttonSwitch < 2" @click="queryNumChanged('9')">9</el-button></el-col>
          <el-col :span="8"><el-button plain type="warning" :disabled="buttonSwitch < 2" @click="queryNumChanged('00')">00</el-button></el-col>
          <el-col :span="8"><el-button plain type="warning" :disabled="buttonSwitch < 2" @click="queryNumChanged('0')">0</el-button></el-col>
          <el-col :span="8"><el-button plain type="warning" :disabled="buttonSwitch < 2" @click="queryNumChanged('.')">.</el-button></el-col>
        </el-row>
      </el-col>
    </el-row>
    <div style="text-align:center">
      <el-button type="warning" plain style="width: 175px">清除</el-button>
      <el-link type="danger" @click="toSimpleSearch">直接搜索&gt;&gt;&gt;</el-link>
      <el-button type="danger" style="width: 175px;margin-right:50px" @click="toResults">确定</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AdvancedQuery',
  data () {
    return {
      formData:{
        textarea: ''
      },
      buttonSwitch: 0,
      queryClip: '',
      queryTemp: ''
    }
  },
  methods: {
    toResults () {
      this.$emit('getMessage', true)
    },
    toSimpleSearch () {
      this.$parent.toSimpleSearch()
    },
    queryChanged (newWord) {
      console.log(this.buttonSwitch)
      if(this.buttonSwitch === 3) {
        if (this.queryTemp != '') {
          this.formData.textarea = '( ' + this.queryTemp + '( '+ this.queryClip + ' )' + ' )' + newWord
        } else {
          this.formData.textarea = this.queryTemp + '( '+ this.queryClip + ' )' + newWord
        }
      }
      else {
        this.formData.textarea += newWord
        this.queryClip += newWord
      }
      if(this.buttonSwitch++ === 3) {
        this.buttonSwitch = 0
        this.queryClip = ''
        this.queryTemp = this.formData.textarea
      }
    },
    queryNumChanged (newNum) {
      this.buttonSwitch = 3
      this.formData.textarea += newNum
      this.queryClip += newNum
    }
  }
}
</script>

<style lang="less" scoped>
.container
{
  padding: 0px 15px;
}
.el-divider__text
{
  color: #9a9a9a;
}
.el-divider--horizontal
{
  margin: 19px 0px;
}
.el-col
{
  text-align: center;
  margin-bottom: 6px
}
.el-button--danger.is-plain
{
  width: 125px;
}
.el-button--warning.is-plain
{
  width: 100px;
}
.numbers
{
  padding: 5px 100px 0px 10px;
  .el-button--warning.is-plain
  {
    width: 50px;
  }
}
.el-form
{
  padding: 5px 0px 0px 100px;
}
.el-form-item
{
  text-align: center; 
  margin-bottom: 0px;
}
.el-link
{
  position: relative;
  left: 471px;
  top: 23px
}
.el-link.is-underline:hover::after
{
  border-bottom: 1px solid #f56c6c
}
::v-deep
{
  .el-textarea__inner
  {
    background-color: #f5f5f5;
  }
  .el-textarea__inner:focus
  {
    border-color: #FF4040;
  }
  .el-form-item__content
  {
    line-height: 19px;
  }
}
</style>
