<template>
  <el-container>

    <el-footer height="100%">
      <el-container>
        <el-aside width="50%">
          <el-table :data="sundry1" ref="sundryTableRef" :show-header="false"
            :cell-style="()=>{return 'text-align: center;font-size:11px;padding:0px'}"
            :header-cell-style="()=>{return 'text-align: center;font-size:11px;padding:0px'}">
          <el-table-column prop="item" :width="50">
          </el-table-column>
          <el-table-column prop="value" :width="70">
            <template slot-scope="scope">
              <span :style="detailListItemClass(scope.row.item, scope.row.value)">{{scope.row.value}}</span>
            </template>
          </el-table-column>
        </el-table>
        </el-aside>
        <el-main>
          <el-table :data="sundry2" ref="sundryTableRef" :show-header="false"
            :cell-style="()=>{return 'text-align: center;font-size:11px;padding:0px'}"
            :header-cell-style="()=>{return 'text-align: center;font-size:11px;padding:0px'}">
          <el-table-column prop="item" :width="50">
          </el-table-column>
          <el-table-column prop="value" :width="70">
            <template slot-scope="scope">
              <span :style="detailListItemClass(scope.row.item, scope.row.value)">{{scope.row.value}}</span>
            </template>
          </el-table-column>
        </el-table>
        </el-main>
      </el-container>
    </el-footer>
  </el-container>
</template>

<script>
export default {
  name: 'SSDetailList',
  props: ['stockid'],
  created () {
    this.stockid === '' ? '' : this.getInfos(this.stockid)
  },
  data () {
    return {
      fiveGearInfo: [
        { gear: '卖五', n1: '39.20', n2: '421' }, { gear: '卖五', n1: '39.20', n2: '421' }, { gear: '卖五', n1: '39.20', n2: '421' }, { gear: '卖五', n1: '39.20', n2: '421' }, { gear: '卖五', n1: '39.20', n2: '421' },
        { gear: '卖五', n1: '39.20', n2: '421' }, { gear: '卖五', n1: '39.20', n2: '421' }, { gear: '卖五', n1: '39.20', n2: '421' }, { gear: '卖五', n1: '39.20', n2: '421' }, { gear: '卖五', n1: '39.20', n2: '421' }
      ],
      largeDealsInfo: [
        { time: '10:30', n1: '30.70', n2: '200' }, { time: '10:30', n1: '30.70', n2: '200' },
        { time: '10:30', n1: '30.70', n2: '200' }, { time: '10:30', n1: '30.70', n2: '200' },
        { time: '10:30', n1: '30.70', n2: '200' }, { time: '10:30', n1: '30.70', n2: '200' },
        { time: '10:30', n1: '30.70', n2: '200' }, { time: '10:30', n1: '30.70', n2: '200' },
        { time: '10:30', n1: '30.70', n2: '200' }, { time: '10:30', n1: '30.70', n2: '200' }
      ],
      sundry1: [
        { item: '最新1', value: '29.31' },
        { item: '涨跌1', value: '-1.51' },
        { item: '涨幅1', value: '-4.90%' },
        { item: '总手1', value: '155.95万' },
        { item: '现手1', value: '6329' }
      ],
      sundry2: [
        { item: '总额1', value: '45.92亿' },
        { item: '涨停1', value: '33.90' },
        { item: '内盘1', value: '79.02万' },
        { item: '市盈1', value: '7.18' },
        { item: '市值1', value: '7391.94亿' }
      ]
    }
  },
  methods: {
    async getInfos (id) {
      const { data: res } = await this.$http.get('selfselection/aside/detail', { params: {stockid: id} })
      if (res.meta.status !== 200) {
          return this.$message.error('数据请求失败')
      }
      const order1 = ['last', 'fall', 'amount', 'sum', 'now', 'rental', 'harden', 'inner', 'ratio', 'value']
      const order2 = ['最新', '涨跌', '涨幅', '总手', '现手', '总额', '涨停', '内盘', '市盈', '市值']
      for (var index = 0; index < this.sundry1.length; index++) {
        this.sundry1[index].item = order2[index]
        if (order2[index] === '涨幅')
          this.sundry1[index].value = res.data.details[order1[index]] + '%'
        else if (order2[index] === '总手') 
          this.sundry1[index].value = res.data.details[order1[index]] + '万'
        else
          this.sundry1[index].value = res.data.details[order1[index]]
      }
      for (var index = 0; index < this.sundry2.length; index++) {
        this.sundry2[index].item = order2[index+this.sundry1.length]
        if (order2[index+this.sundry1.length] === '涨幅')
          this.sundry2[index].value = res.data.details[order1[index+this.sundry1.length]] + '%'
        else if (order2[index+this.sundry1.length] === '内盘') 
          this.sundry2[index].value = res.data.details[order1[index+this.sundry1.length]] + '万'
        else if (order2[index+this.sundry1.length] === '总额' || order2[index+this.sundry1.length] === '市值') 
          this.sundry2[index].value = res.data.details[order1[index+this.sundry1.length]] + '亿'
        else
          this.sundry2[index].value = res.data.details[order1[index+this.sundry1.length]]
      }
      // this.total = res.data.totalpage
      console.log(res)
      // return this.$message.success('数据请求成功')
    },
    detailListItemClass (item, value) {
      switch (item) {
        case '最新':
        case '涨跌':
        case '涨停':
          return this.upOrDown(value)
          break;
        case '涨幅':
          return this.upOrDown(value.replace('%',''))
          break;
        case '总手':
        case '现手':
        case '市盈':
        case '市值':
          return 'color:#f3a672'
          break;
        case '总额':
        case '内盘':
          return 'color:#49c3f0'
          break;
      }
    },
    upOrDown (v) {
      return v == 0 ?  'color:black' : v > 0 ? 'color:#f04844' : 'color:#26aa5e'
    }
  }
}
</script>

<style lang="less" scoped>
.el-main
{
    padding: 0px;
}
.el-footer
{
  padding: 0px;
}
::v-deep
{
  .el-table .cell
  {
    padding: 0px !important;
  }
}
</style>
