<template>
  <div class="chart-custom">
    <i-echarts :option="option" @ready="onReady" resizable></i-echarts>
  </div>
</template>

<script>
import { THEME_COLOR, MA_COLOR } from '@/config/stock'
import { getMinusData, ratioCalculate } from '@/utils/stock'
import initMinusData from '@/utils/initMinusData'

export default {
  name: 'LiveChart',
  data() {
    return {
      echarts: null,
      option: {}
    }
  },
  methods: {
    onReady(instance, echarts) {
      this.echarts = echarts

      let mDatas = getMinusData(initMinusData, 'us')

      console.log('mDatas', mDatas)

      this.option = {
        tooltip: {
          //弹框指示器
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          },
          formatter(params, ticket, callback) {
            let i = params[0].dataIndex

            let colorTag =
              mDatas.priceArr[i] > initMinusData.yestclose
                ? 'style="color:#ff4242"'
                : 'style="color:#26bf66"'

            let html = `
              <div class="commColor" style="width:100px;">
                <div>
                  当前价<span ${colorTag}>${mDatas.priceArr[i]}</span>
                </div>
                <div>
                  均价<span ${colorTag}>${mDatas.avgPrice[i]}</span>
                </div>
                <div>
                  涨幅<span ${colorTag}>${ratioCalculate(
              mDatas.priceArr[i],
              initMinusData.yestclose
            )}</span>
                </div>
                <div>
                  成交量<span  ${colorTag}>${mDatas.vol[i]}</span>
                </div>
              </div>
            `
            return html
          }
        },
        legend: {
          //图例控件,点击图例控制哪些系列不显示
          icon: 'rect',
          type: 'scroll',
          itemWidth: 14,
          itemHeight: 2,
          left: 0,
          top: '-1%',
          textStyle: {
            fontSize: 12,
            color: '#0e99e2'
          }
        },
        axisPointer: {
          show: true
        },
        color: [MA_COLOR.COLOR_5, MA_COLOR.COLOR_10],
        grid: [
          {
            id: 'gd1',
            left: '0%',
            right: '1%',
            height: '67.5%', //主K线的高度,
            top: '5%'
          },
          {
            id: 'gd2',
            left: '0%',
            right: '1%',
            height: '67.5%', //主K线的高度,
            top: '5%'
          },
          {
            id: 'gd3',
            left: '0%',
            right: '1%',
            top: '75%',
            height: '19%' //交易量图的高度
          }
        ],
        xAxis: [
          //==== x轴
          {
            //主图
            gridIndex: 0,
            data: mDatas.times,
            axisLabel: {
              //label文字设置
              show: false
            },
            splitLine: {
              show: false
            }
          },
          {
            show: false,
            gridIndex: 1,
            data: mDatas.times,
            axisLabel: {
              //label文字设置
              show: false
            },
            splitLine: {
              show: false
            }
          },
          {
            //交易量图
            splitNumber: 2,
            type: 'category',
            gridIndex: 2,
            data: mDatas.times,
            axisLabel: {
              //label文字设置
              color: '#9b9da9',
              fontSize: 10
            }
          }
        ],
        yAxis: [
          //y轴
          {
            gridIndex: 0,
            scale: true,
            splitNumber: 3,
            axisLabel: {
              //label文字设置
              inside: true, //label文字朝内对齐
              fontWeight: 'bold',
              color(val) {
                if (val == initMinusData.yestclose) return '#aaa'
                return val > initMinusData.yestclose ? THEME_COLOR.UP : THEME_COLOR.DOWN
              }
            },
            z: 4,
            splitLine: {
              //分割线设置
              show: false,
              lineStyle: {
                color: '#181a23'
              }
            }
          },
          {
            scale: true,
            gridIndex: 1,
            splitNumber: 3,
            position: 'right',
            z: 4,
            axisLabel: {
              //label文字设置
              color(val) {
                if (val == initMinusData.yestclose) return '#aaa'
                return val > initMinusData.yestclose ? THEME_COLOR.UP : THEME_COLOR.DOWN
              },
              inside: true, //label文字朝内对齐
              fontWeight: 'bold',
              formatter(val) {
                let resul = ratioCalculate(val, initMinusData.yestclose)
                return Number(resul).toFixed(2) + ' %'
              }
            },
            splitLine: {
              //分割线设置
              show: false,
              lineStyle: {
                color: '#181a23'
              }
            },
            axisPointer: {
              show: true,
              label: {
                formatter(params) {
                  //计算右边Y轴对应的当前价的涨幅比例
                  return ratioCalculate(params.value, initMinusData.yestclose) + '%'
                }
              }
            }
          },
          {
            //交易图
            gridIndex: 2,
            z: 4,
            splitNumber: 3,
            axisLine: {
              onZero: false
            },
            axisTick: {
              show: false
            },
            splitLine: {
              show: false
            },
            axisLabel: {
              //label文字设置
              color: '#c7c7c7',
              inside: true, //label文字朝内对齐
              fontSize: 8
            }
          }
        ],
        dataZoom: [],
        //animation:false,//禁止动画效果
        // backgroundColor: THEME_COLOR.BG,
        blendMode: 'source-over',
        series: [
          {
            name: '当前价',
            type: 'line',
            data: mDatas.priceArr,
            smooth: true,
            symbol: 'circle', //中时有小圆点
            lineStyle: {
              opacity: 0.8,
              color: '#39afe6',
              width: 1
            },
            areaStyle: {
              color: {
                type: 'linear',
                x: 0,
                y: 0,
                x2: 0,
                y2: 1,
                colorStops: [
                  {
                    offset: 0,
                    color: 'rgba(0, 136, 212, 0.7)' // 0% 处的颜色
                  },
                  {
                    offset: 1,
                    color: 'rgba(0, 136, 212, 0.02)' // 100% 处的颜色
                  }
                ],
                global: false // 缺省为 false
              },
              shadowColor: 'rgba(0, 0, 0, 0.1)',
              shadowBlur: 10
            }
          },
          {
            name: '均价',
            type: 'line',
            data: mDatas.avgPrice,
            smooth: true,
            symbol: 'circle',
            lineStyle: {
              //标线的样式
              opacity: 0.8,
              color: '#da6ee8',
              width: 1
            }
          },
          {
            type: 'line',
            data: mDatas.priceArr,
            smooth: true,
            symbol: 'none',
            gridIndex: 1,
            xAxisIndex: 1,
            yAxisIndex: 1,
            //标线的样式
            normal: {
              width: 0
            }
          },
          {
            name: '成交量',
            type: 'bar',
            gridIndex: 2,
            xAxisIndex: 2,
            yAxisIndex: 2,
            data: mDatas.vol,
            barWidth: '60%',
            itemStyle: {
              color(params) {
                return mDatas.priceArr[params.dataIndex] > mDatas.priceArr[params.dataIndex - 1]
                  ? THEME_COLOR.UP
                  : THEME_COLOR.DOWN
              }
            }
          }
        ]
      }
    }
  }
}
</script>

<style lang="less" scoped>
.chart-custom {
  width: 100%;
  height: calc(100% - 30px);
}
</style>
