<script>
// import echarts from 'echarts'
export default {
  created () {
    this.getInfos()
  },
  data () {
    return {
      radio: '全部板块',
      userid: 0,
      queryInfo: {
        pagenum: 1,
        pagesize: 1
      },
      total: 10,
      tableData: [
        {
          name: '饲料',
          uprange: 4.81,
          leading: {
            name: '新希望',
            vol: 28.98,
            pre: 7.68
          },
          upspeed: 0.06,
          change: '---',
          volume: 1.16,
          ued: {
            up: 9,
            equal: 0,
            down: 7
          },
          uprange5day: 6.97,
          net: 1.58
        }
      ]
    }
  },
  methods: {
    async getInfos () {
      const { data: res } = await this.$http.get('market/block/top')
      if (res.meta.status !== 200) {
        return this.$message.error('数据请求失败')
      }
      // this.tableData = res.data.detail
      // this.total = res.data.totalpage
      console.log(res)
      // return this.$message.success('数据请求成功')
    },
    async getTableData () {
      const { data: res } = await this.$http.get('market/indexs/AStock', { params: this.queryInfo })
      if (res.meta.status !== 200) {
          return this.$message.error('数据请求失败')
      }
      this.tableData = res.data.detail
      this.total = res.data.totalpage
      // console.log('请求参数')
      // console.log(this.queryInfo)
      // console.log('数据')
      console.log(res)
      // return this.$message.success('数据请求成功')
    },
    handleSizeChange (val) {
        this.queryInfo.pagesize = val
        this.getTableData()
        // console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
        this.queryInfo.pagenum = val
        this.getTableData()
        // console.log(`当前页: ${val}`)
    }
  }
}
</script>

