<template>
  <el-container>
    <el-aside width="259px" v-show="this.carryData">
      <el-button-group style="width:240px;margin:3px 0px 3px 0px">
        <el-button type="info" style="width:50%" @click="toAndvancedQuery"><i class="el-icon-arrow-left"></i> 查询 </el-button>
        <el-button type="danger" class="addNewStockBtn" @click="toAddNewStocks"> 添加 <i class="el-icon-plus"></i></el-button>
      </el-button-group>
      <el-table :data="tableData" ref="asideTableRef" @row-click="onAsideTableClick"
        @expand-change="onAsideTableExpandChange" :row-key="getRowKeys"
        :expand-row-keys="asideTableExpendPowKey" highlight-current-row
        style="width: 95%" :cell-style="()=>{return 'background-color:#f2f2f2;text-align: center'}"
        :header-cell-style="()=>{return 'background-color:#e1e1e1;text-align: center'}">
        <el-table-column label="股票" :width="85">
          <template slot-scope="scope">
            <p class="tableItemTitle">{{scope.row.stock_name}}</p>
            <p class="tableItemTitle">{{scope.row.stock_code}}</p>
          </template>
        </el-table-column>
        <el-table-column label="最新" prop="last" :width="75">
        </el-table-column>
        <el-table-column label="涨幅" :width="65">
          <template slot-scope="scope">
            <el-tag effect="dark" :type="scope.row.up_down_rate>0?'danger':'success'" size="small">
              <span v-if="scope.row.up_down_rate>0">+</span>
              <span>{{scope.row.up_down_rate}}%</span>
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column type="expand" :width="15">
          <template :slot-scope="scope">
            <!-- <el-row>
              <el-col :span="12">五档</el-col>
              <el-col :span="12">大单</el-col>
            </el-row> -->
            <SSDetailList :stockid="selectedStockid"></SSDetailList>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination :current-page="paginationInfo.pagenum" 
        background :total="paginationInfo.total"
        layout="prev, pager, next" hide-on-single-page
        @current-change="handleCurrentChange">
      </el-pagination>
    </el-aside>
    <el-main>
      <el-container>
        <el-header height="500px">
          <el-tabs v-model="topActiveName" class="topTabs">
            <el-tab-pane label="K线图" name="kchart" style="height:500px">
              <KCharts v-if="topActiveName==='kchart'" :toReSize="carryData" :stockId="selectedStockid"
                style="position: relative; left: -21px" :detailParams="kChartsParams"></KCharts>
            </el-tab-pane>
            <el-tab-pane label="分时图" name="timeline" style="height:500px">
              <TimeSharing v-if="topActiveName==='timeline'" :toReSize="carryData" :stockId="selectedStockid"
                style="position: relative; left: -21px; top:-35px"></TimeSharing>
            </el-tab-pane>
          </el-tabs>
          <el-button type="info" plain @click="toMore" size="mini" style="position: relative; left: 87%;bottom: 533px">更多设置></el-button>
        </el-header>
        <el-main>
          <!-- <el-menu :default-active="'funds'" mode="horizontal"
            background-color="#f15151" text-color="#f4AAAA"
            active-text-color="#ffffff" @select="onSelected">
            <el-menu-item index="funds">资金</el-menu-item>
            <el-menu-item index="infos">资讯</el-menu-item>
            <el-menu-item index="f10">F10</el-menu-item>
          </el-menu> -->
          <el-tabs v-model="activeName"  class="bottomTabs">
            <el-tab-pane label="资金" name="first" style="height:1110px">
              <SSFundInfo2 v-if="activeName==='first'" :stockId="selectedStockid"
                :toReSize="carryData" style="margin: 0 15px 0 15px"></SSFundInfo2>
            </el-tab-pane>
            <el-tab-pane label="资讯" name="second">
              <SSInfo v-if="activeName==='second'" :stockId="selectedStockid"></SSInfo>
            </el-tab-pane>
            <el-tab-pane label="大事提醒" name="third">
              <SSEvents v-if="activeName==='third'" :stockId="selectedStockid"></SSEvents>
            </el-tab-pane>
            <el-tab-pane label="主营构成" name="fourth">
              <SSComposition v-if="activeName==='fourth'" :stockId="selectedStockid"></SSComposition>
            </el-tab-pane>
            <el-tab-pane label="股本结构" name="fifth">
              <SSStructure v-if="activeName==='fifth'" :stockId="selectedStockid"></SSStructure>
            </el-tab-pane>
            <el-tab-pane label="财务分析" name="sixth">
              <SSAnalyse v-if="activeName==='sixth'" :stockId="selectedStockid"></SSAnalyse>
            </el-tab-pane>
            <!-- <el-tab-pane label="TEST" name="seventh">
              <SSFundsInfo></SSFundsInfo>
            </el-tab-pane> -->
          </el-tabs>
        </el-main>
      </el-container>
    </el-main>
    <!-- <el-dialog title="添加自选股" :visible.sync="addDialogVisible"> -->
    <el-dialog title="添加自选股" :visible.sync="addDialogVisible" fullscreen>
      <AddNewStocks :stocks="rawTableData" :stockId="selectedStockid"></AddNewStocks>
    </el-dialog>
    <!-- <el-dialog :visible.sync="dialogVisible" width="650px"> -->
    <el-dialog :visible.sync="dialogVisible" fullscreen>
      <el-form :model="formData" style="width:100%">
        <el-form-item label="时间区间">
          <el-date-picker
            v-model="formData.pickTime"
            type="daterange"
            align="right"
            unlink-panels
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            :picker-options="pickerOptions">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="年|周|日K线">
          <el-select v-model="formData.elSelectValue" placeholder="请选择" style="width:350px">
            <el-option
              v-for="item in this.formElSelectOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
              style="text-align:center">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="K线图指数">
          <el-row style="padding-top: 9px">
            <el-col :span="8" style="border-top:0px">
              <el-switch
                style="display: block"
                v-model="formData.ma"
                active-color="#b368ff"
                active-text="MA指数"
                @change="onSwitchChange('ma')">
              </el-switch>
            </el-col>
            <el-col :span="8" style="border-top:0px">
              <el-switch
                style="display: block"
                v-model="formData.boll"
                @change="onSwitchChange('boll')"
                active-color="#b368ff"
                active-text="BOLL指数">
              </el-switch>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="其他指数">
          <el-row style="padding-top: 9px">
            <el-col :span="8" style="border-top:0px">
              <el-switch
                style="display: block"
                v-model="formData.kdj"
                active-color="#b368ff"
                active-text="KDJ指数"
                @change="onSwitchChange('ma')">
              </el-switch>
            </el-col>
            <el-col :span="8" style="border-top:0px">
              <el-switch
                style="display: block"
                v-model="formData.macd"
                @change="onSwitchChange('boll')"
                active-color="#b368ff"
                active-text="MACD指数">
              </el-switch>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="MA参数①">
          <el-input v-model="formData.maParam[0]" onkeyup="this.value=this.value.replace(/[^\d.]/g,'');" maxlength="2" style="width:350px"></el-input>
        </el-form-item>
        <el-form-item label="MA参数②">
          <el-input v-model="formData.maParam[1]" onkeyup="this.value=this.value.replace(/[^\d.]/g,'');" maxlength="2" style="width:350px"></el-input>
        </el-form-item>
        <el-form-item label="MA参数③">
          <el-input v-model="formData.maParam[2]" onkeyup="this.value=this.value.replace(/[^\d.]/g,'');" maxlength="2" style="width:350px"></el-input>
        </el-form-item>
        <el-form-item label="BOLL参数">
          <el-input v-model="formData.bollParam" onkeyup="this.value=this.value.replace(/[^\d.]/g,'');" maxlength="2" style="width:350px"></el-input>
        </el-form-item>
      </el-form>
    </el-dialog>
  </el-container>
</template>

<script>
  import SSDetailList from '../SelfSelection/DetailList.vue'
  import SSFundsInfo from '../SelfSelection/FundsInfo.vue'
  import SSInfo from '../SelfSelection/Infos.vue'
  import SSEvents from '../SelfSelection/Events.vue'
  import SSComposition from '../SelfSelection/Composition.vue'
  import SSStructure from '../SelfSelection/Structure.vue'
  import SSAnalyse from '../SelfSelection/Analyse.vue'
  import SSFundInfo2 from '../SelfSelection/FundsInfo2.vue'
  import AddNewStocks from './AddNewStocks.vue'
  import KCharts from '../SelfSelection/KCharts.vue'
  import TimeSharing from '../SelfSelection/TimeSharing.vue'
  export default {
    components: { SSDetailList, SSFundsInfo, SSInfo, SSEvents, SSComposition,
      SSStructure, SSAnalyse, SSFundInfo2, AddNewStocks, KCharts, TimeSharing },
    props: ['visible', 'carryData'],
    name: 'QueryResults',
    created () {
      this.getInfos().then((result) => {
        this.selectedStockid = this.tableData[0].stock_code
        console.log(this.selectedStockid)
      })
      this.kChartsParams = this.formData
    },
    computed: {
      toMAorBOLL() {
        this.formData.maORboll = !this.formData.maORboll
        return this.formData.maORboll
      }
    },
    data () {
      return {
        activeName: 'first',
        topActiveName: 'kchart',
        asideTableExpendPowKey: [],
        addDialogVisible: false,
        selectedStockid: '',
        dialogVisible: false,
        kChartsParams: {},
        paginationInfo: {
          total: 30,
          pagenum: 1
        },
        formElSelectOptions: [{
          value: '0',
          label: '年K线'
        }, {
          value: '1',
          label: '周K线'
        }, {
          value: '100',
          label: '月K线',
          disabled: true
        }, {
          value: '2',
          label: '日K线'
        }],
        pickerOptions: {
          shortcuts: [{
            text: '最近三月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近半年',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 180);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一年',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 365);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近两年',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 730);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三年',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 1095);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        formData: {
          pickTime: [ "2020-01-26", "2021-01-26" ],
          maParam: [3, 5, 10],
          bollParam: 20,
          ma: true,
          boll: false,
          kdj: false,
          macd: false,
          elSelectValue: '2'
        },
        formDataRules: {
            ma: [
                { required: true, message: '请输入参数', trigger: 'blur' },
                { type:'number', message: '请输入数字', trigger: ['blur', 'change'], transform: (value) => Number(value)}
            ]
        },
        tableData: [
          // {
          //   stock_code: 'SZ399001',
          //   stock_name: '深证成指',
          //   last: 10033.36,
          //   up_down_rate: '+0.04%'
          // },
          // {
          //   stock_code: 'SZ399002',
          //   stock_name: '深证成指',
          //   last: 10033.36,
          //   up_down_rate: '+0.04%'
          // },
          // {
          //   stock_code: 'SZ399003',
          //   stock_name: '深证成指',
          //   last: 10033.36,
          //   up_down_rate: '+0.04%'
          // }
        ],
        rawTableData: []
      }
    },
    methods: {
      async getInfos () {
        var searchResult = JSON.parse(window.sessionStorage.getItem('searchResult'))
        var queryString = ''
        if(Array.isArray(searchResult)) {
          searchResult.forEach(item => {
            queryString += item.stock_code + ','
          })
          queryString = queryString.substring(0, queryString.length - 1)
        }
        else {
          queryString = searchResult.stock_code
        }
        const { data: res } = await this.$http.get('selfselection/queryResults', { params: {stock_code:queryString} })
        this.rawTableData = res.data.details
        for (let i = 0; i < this.rawTableData.length; i++) {
          this.rawTableData[i].up_down_rate = 
            parseFloat(this.rawTableData[i].up_down_rate).toFixed(2)
        }
        this.tableData = this.rawTableData.slice(0,10)
        this.paginationInfo.total = this.rawTableData.length
        this.paginationInfo.pagenum = 1
        // this.total = res.data.totalpage
        // return this.$message.success('数据请求成功')
      },
      onAsideTableClick (row, column, event) {
        this.$refs.asideTableRef.toggleRowExpansion(row)
      },
      onAsideTableExpandChange (row, expandedRows) {
        this.asideTableExpendPowKey = []
        if (expandedRows.length > 0) {
          if (row) {
            this.asideTableExpendPowKey.push(row.stock_code)
          }
        }
        this.selectedStockid = row.stock_code
      },
      getRowKeys (row) {
        return row.stock_code
      },
      onSelected (key, keyPath) {
        for (var item in this.visible) {
          if (this.visible[item]) {
            this.visible[item] = false
            // console.log(this.visible[item])
          }
        }
        this.visible[key] = true
      },
      handleCurrentChange(val) {
        this.tableData = this.rawTableData.slice((val - 1) * 10,val * 10)
        this.paginationInfo.pagenum = val
      },
      toAndvancedQuery () {
        this.$emit('getMessage', false)
      },
      toAddNewStocks () {
        this.addDialogVisible = !this.addDialogVisible
      },
      toMore () {
        this.dialogVisible = !this.dialogVisible
      },
      onSwitchChange(v) {
        v === 'ma' ? this.formData.boll = !this.formData.ma : this.formData.ma = !this.formData.boll
      }
    },
    watch: {
      visible(v) {
        if (v)
          this.$refs.veCandleRef.resize()
      },
      "$route.query"() {
        this.getInfos()
      },
      dialogVisible(v) {
        if(!v)
          this.kChartsParams = this.formData
      }
    }
  }
</script>

<style lang="less" scoped>
.el-collapse
{
  width: 240px !important;
}
.el-container
{
  min-height: 100%;
}
.el-aside
{
  background-color: #f2f2f2;
  border-right:3px solid #ffffff;
  text-align: center;
}
.el-main
{
  padding: 0px;
}
