<template>
  <div>
    <el-form ref="modifyInfoFormRef" :model="formData"
      :rules="formDataRules" label-width="53px">
      <el-form-item label="昵称" prop="username">
        <el-input v-model="formData.username">
        </el-input>
      </el-form-item>
      <el-form-item label="头像" prop="avatar">
        <el-upload
          class="avatar-uploader"
          action="https://jsonplaceholder.typicode.com/posts/"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload">
          <img v-if="formData.avatar" :src="formData.avatar" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="简介" prop="introduction">
        <el-input v-model="formData.introduction" maxlength="85" show-word-limit type="textarea">
        </el-input>
      </el-form-item>
      <el-form-item label="年龄" prop="age">
        <el-input v-model="formData.age" onkeyup="value=value.replace(/[^\d]/g,'')" maxlength="3">
        </el-input>
      </el-form-item>
      <el-form-item label="工作" prop="job">
        <el-input v-model="formData.job" maxlength="50">
        </el-input>
      </el-form-item>
      <el-form-item label="地址" prop="address">
        <el-input v-model="formData.address" maxlength="50">
        </el-input>
      </el-form-item>
    </el-form>
    <el-button type="danger" class="submitBtn" @click="submitForm()">
      修改信息
    </el-button>
  </div>
</template>

<script>
export default {
  name: 'PCModify',
  data () {
    return {
      formData: {
        username: '',
        avatar: '',
        introduction: '',
        age: '',
        job: '',
        address: '',
        hobby: ''
      },
      formDataRules: {
        username: [
          { required: true, message: '请输入昵称', trigger: 'blur' },
          { min: 3, max: 11, message: '长度在 6 到 11 个字符', trigger: 'blur' }
        ],
        avatar: [
          { required: true, message: '请上传头像', trigger: 'blur' },
        ],
        introduction: [
          { required: true, message: '请输入自我简介', trigger: 'blur' }
        ],
        avatar: [
          { required: true, message: '请输入昵称', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleAvatarSuccess(res, file) {
      this.formData.avatar = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isPNG = file.type === 'image/png';
      const isLt500KB = file.size / 1024 < 500;

      if (!isPNG) {
        this.$message.error('上传头像图片只能是 png 格式!');
      }
      if (!isLt500KB) {
        this.$message.error('上传头像图片大小不能超过 500KB!');
      }
      return isPNG && isLt500KB;
    }
  }
}
</script>

<style lang="less" scoped>
.submitBtn
{
  // width: 250px;
  width: 100%;
  font-size: 20px;
  // margin-left: 185px;
  display: block;
  margin: 0 auto;
}
::v-deep
{
  .avatar-uploader .el-upload {
    border: 1px dashed #c4c4c4;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color:#409eff;
    i {
      color: #409eff;
    }
  }
  .avatar-uploader-icon {
    font-size: 14px;
    color: #8c939d;
    width: 89px;
    height: 89px;
    line-height: 89px;
    text-align: center;
  }
  .avatar {
    // width: 89px;
    height: 89px;
    display: block;
  }
}
</style>
