<template>
  <div class="echarts">
    <i-echarts :option="option" @ready="onReady" resizable></i-echarts>
  </div>
</template>

<script>
import { THEME_COLOR, MA_COLOR } from '@/config/stock'
import { splitKdjData } from '@/utils/stock'
import initKdjData from '@/utils/initKdjData'

export default {
  name: 'LiveChart',
  data() {
    return {
      echarts: null,
      option: {},
      // 初始化获取KDJ的数据
      KDJINIT:[]
    }
  },
  methods: {
    onReady(instance, echarts) {
      this.echarts = echarts
      // let data = splitKdjData(KDJINIT)
     let data = splitKdjData(initKdjData)


     this.addKeydownChangeZoom()
    
      this.option = {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          },
          backgroundColor: 'rgba(245, 245, 245, 0.8)',
          borderWidth: 1,
          borderColor: '#ccc',
          padding: 10,
          textStyle: {
            color: '#000'
          },
          extraCssText: 'width: 170px'
        },
        color: [MA_COLOR.COLOR_5, MA_COLOR.COLOR_10, MA_COLOR.COLOR_20],
        legend: {
          data: ['K', 'D', 'J'],
          top: '6%'
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: data.categoryData
        },
        yAxis: {
          type: 'value',
          min: 'dataMin',
          max: 'dataMax',
          boundaryGap: [0, '100%']
        },
        dataZoom: [
          {
            type: 'inside',
            start: 10,
            end: 100
          },
          {
            show: true,
            type: 'slider',
            y: '90%',
            start: 10,
            end: 100
          }
        ],
        series: [
          {
            name: 'K',
            type: 'line',
            smooth: true,
            symbol: 'none',
            sampling: 'average',
            data: data.k
          },
          {
            name: 'D',
            type: 'line',
            smooth: true,
            symbol: 'none',
            sampling: 'average',

            data: data.d
          },
          {
            name: 'J',
            type: 'line',
            smooth: true,
            symbol: 'none',
            sampling: 'average',
            data: data.j
          }
        ]
      }
    },
    addKeydownChangeZoom() {
      window.addEventListener('keydown', ev => {
        // console.log(ev)
      })
    }
  },
  // 在也页面前的过程中 去后台拿取KDJ的数据
  // 这里的id我目前不知道从哪里那数据 自己造的  // ?id='+"603686.SH"
  // created(){
  //  this.$http.get('kdj/kdjInfo?id='+"603686.SH").then(res=>{
  //    console.log(res.data.data.maCDInfo,"这是拿去的KDJ的数据")
  //     console.log(res.data.data,"这是拿去的KDJ的数据")
  //    console.log(this.$router)
  //    // 将拿去的数据进行赋值
  //   this.KDJINIT =  res.data.data.maCDInfo;
  //  }).catch(err=>{
  //    console.log(err)
  //  })
  // }
}
</script>

<style lang="less" scoped>
.echarts {
  height: 100%;
}
</style>
