<template>
  <!-- <el-tooltip content="联系我们"> -->
  <el-popover v-model="visible">
    <div class="msg-header">留言板</div>
    <div class="msg-main">
      <el-input
        type="textarea"
        :rows="6"
        placeholder="请输入内容"
        v-model="textarea"
        :disabled="loading">
      </el-input>
    </div>
    <div class="msg-footer">
      <el-button size="mini" type="text" @click="msgCancel">取消</el-button>
      <el-button type="danger" size="mini" :loading="loading" @click="msgSubmit">确定</el-button>
    </div>
    <el-button slot="reference" type="danger" circle><i class="el-icon-chat-dot-round"></i></el-button>
  </el-popover>
  <!-- </el-tooltip> -->
</template>

<script>
export default {
  data() {
    return  {
      visible: false,
      loading: false,
      textarea: ''
    }
  },
  methods: {
    msgCancel () {
      this.visible = false;
      // 正在提交时 点击取消不清空输入内容
      if (!this.loading) {
        this.textarea = '';
      }
    },
    msgSubmit () {
      if (! this.textarea) {
        return this.$message({
          message: '请输入留言内容',
          type: 'warning'
        });
      }

      this.loading = true;

      console.log(this.textarea);
      this.visible = false;
      this.loading = false;
      this.textarea = '';
      return this.$message({
        message: '留言成功，正在火速处理',
        type: 'success'
      });

      return this.$message({
        message: '出了点小问题，请稍后再试',
        type: 'error'
      });
    }
  }
}
</script>

<style lang="less" scoped>
.msg-header {
  padding: 10px;
  width: 300px;
  color: #303133;
  font-size: 16px;
  line-height: 1;
}
.msg-main {
  padding: 5px 10px 10px;
}
.msg-footer {
  padding: 10px;
  text-align: right;
}
.el-button--text {
  color: #f56c6c;
}
.el-button--text:hover, .el-button--primary:focus {
  color: #f78989 !important;
}
.el-button i {
  font-size: 21px;
}
</style>