<template>
  <div class="container-router">
    <div class="title">
      <el-row>
        <el-col :offset="1" :span="5">
          <el-avatar :src="require('@/assets/images/avatar.png')" :size="85"></el-avatar>
        </el-col>
        <el-col :offset="2" :span="14">
          <p style="font-size: 33px;text-align:start">犇犇犇</p>
          <p style="color:#555555;font-style:oblique;text-align:start">个性签名………………</p>
        </el-col>
      </el-row>
      <el-row>
        <el-col :offset="1">
          <div class="group-button">
            <el-tooltip effect="dark" content="详细信息" placement="bottom" :enterable="false">
              <el-button type="danger" size="middle" icon="el-icon-document" @click="toDetailedInformation">详细信息</el-button>
            </el-tooltip>

            <el-tooltip effect="dark" content="修改信息" placement="bottom" :enterable="false">
              <el-button type="danger" size="middle" icon="el-icon-edit-outline" @click="toModifyInformation">修改</el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="返回主页" placement="bottom" :enterable="false">
              <el-button type="danger" size="middle" icon="el-icon-back" @click="signOut">退出</el-button>
            </el-tooltip>
          </div>
        </el-col>
      </el-row>
    </div>
    <el-table :data="tableData" border height="443" style="width:95%"
        :cell-style="()=>{return 'text-align: center'}"
        :header-cell-style="()=>{return 'background:#fef1f0;text-align: center'}">
        <el-table-column type="index" label="#" width="39px" fixed="left">
        </el-table-column>
        <el-table-column prop="name" label="名称" fixed="left">
        </el-table-column>
        <el-table-column prop="last" label="最新">
        </el-table-column>
        <el-table-column prop="uprange" label="涨幅">
        </el-table-column>
        <el-table-column prop="charge" label="涨跌">
        </el-table-column>
        <el-table-column prop="prevclose" label="昨收">
        </el-table-column>
        <el-table-column prop="volume" label="成交量">
        </el-table-column>
        <el-table-column prop="turnover" label="成交额">
        </el-table-column>
        <el-table-column prop="max" label="最高">
        </el-table-column>
        <el-table-column prop="min" label="最低">
        </el-table-column>
    </el-table>
    <!-- @size-change="handleSizeChange" @current-change="handleCurrentChange" -->
    <el-pagination :current-page="this.queryInfo.pagenum"
      :page-sizes="[1, 2, 5, 10, 20, 100, 200]"
      :page-size="queryInfo.pagesize"
      :total="this.total" background
      layout="total, sizes, prev, pager, next, jumper"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      class="pagination">
    </el-pagination>
    <!-- <el-dialog :visible.sync="setDetailDialogVisible" width="600px" top="85px"> -->
    <el-dialog :visible.sync="setDetailDialogVisible" title="查看个人信息" fullscreen>
      <PCDetail @handleChildInfo="handleChildInfo"></PCDetail>
    </el-dialog>
    <!-- <el-dialog :visible.sync="setModifyDialogVisible" width="790px" top="55px"> -->
    <el-dialog :visible.sync="setModifyDialogVisible" title="修改个人信息" fullscreen>
      <PCModify></PCModify>
    </el-dialog>
    <!-- <el-dialog :visible.sync="setBindPhoneDialogVisible" title="绑定手机" width="600px" top="150px"> -->
    <el-dialog :visible.sync="setBindPhoneDialogVisible" title="绑定手机" fullscreen>
      <BindPhone></BindPhone>
    </el-dialog>
    <!-- <el-dialog :visible.sync="setBindWeChartDialogVisible" title="绑定微信" width="600px" top="150px"> -->
    <el-dialog :visible.sync="setBindWeChartDialogVisible" title="绑定微信" fullscreen>
      <BindWeChart></BindWeChart>
    </el-dialog>
    <!-- <el-dialog :visible.sync="setBindQQDialogVisible" title="绑定QQ" width="600px" top="150px"> -->
    <el-dialog :visible.sync="setBindQQDialogVisible" title="绑定QQ" fullscreen>
      <BindQQ></BindQQ>
    </el-dialog>
  </div>
</template>

<script>
import PCDetail from './Detail'
import PCModify from './Modify'
import BindPhone from './BindPhone'
import BindWeChart from './BindWeChart'
import BindQQ from './BindQQ'

export default {
  components: { PCDetail, PCModify, BindPhone, BindWeChart, BindQQ },
  data () {
    return {
      queryInfo: {
        pagenum: 1,
        pagesize: 1
      },
      total: 10,
      tableData: [
        {
          name: 'NULL',
          last: 0,
          uprange: 0,
          charge: 0,
          prevclose: 0,
          volume: 0,
          turnover: 0,
          max: 0,
          min: 0,
          amplitude: 0,
          accelerate: 0
        }
      ],
      setModifyDialogVisible: false,
      setDetailDialogVisible: false,
      setBindPhoneDialogVisible: false,
      setBindWeChartDialogVisible: false,
      setBindQQDialogVisible: false
    }
  },
  methods: {
    signOut () {
      this.$router.push('/welcome')
    },
    toModifyInformation () {
      this.setModifyDialogVisible = true
    },
    toDetailedInformation () {
      this.setDetailDialogVisible = true
    },
    async getTableData () {
      const { data: res } = await this.$http.get('market/indexs/AStock', { params: this.queryInfo })
      if (res.meta.status !== 200) {
          return this.$message.error('数据请求失败')
      }
      this.tableData = res.data.detail
      this.total = res.data.totalpage
      // console.log('请求参数')
      // console.log(this.queryInfo)
      // console.log('数据')
      console.log(res)
      // return this.$message.success('数据请求成功')
    },
    handleSizeChange (val) {
        this.queryInfo.pagesize = val
        this.getTableData()
        // console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
        this.queryInfo.pagenum = val
        this.getTableData()
        // console.log(`当前页: ${val}`)
    },
    handleChildInfo (switchVisible) {
      switch (switchVisible) {
        case 0:
           this.setBindPhoneDialogVisible = true
           break;
        case 1:
          this.setBindWeChartDialogVisible = true
          break;
        case 2:
          this.setBindQQDialogVisible = true
          break;
      }
    }
  }
}
</script>

<style lang="less" scoped>
.container-router {
  width: 100%;
  height: 100%;
  font-size: 16px;
  overflow: hidden scroll;
}
.title
{
  // background:url('../../../assets/images/bg.jpg');
  // height: 96px;
  padding-top: 16px;
}
.group-button {
  text-align: left;
  margin-top: 12px;
  margin-bottom: 6px;
}
.el-row
{
  display: flex;
  align-items: center;
}
.el-col
{
  text-align: center;
  p
  {
    line-height: 0px;
  }
}
.el-button
{
  // width: 69px;
  // height: 45px;
  margin: 0 10px 10px 0 !important;
}
.el-table
{
  margin-left: 2.5%;
}
.el-pagination
{
  margin: 10px 0 10px 2.5%;
  width: 100%;
  overflow-x: scroll !important;
}
::v-deep
{
  .el-pagination.is-background .el-pager li:not(.disabled).active
  {
    background-color: #f14747;   // 进行修改选中项背景和字体
    color: #EEEEEE;
  }
  .el-pagination.is-background .el-pager li:not(.active):hover
  {
    color:#ff4040;
  }
  .el-dialog__headerbtn:hover .el-dialog__close
  {
    color: #ff4040;
  }
  .el-dialog {
    overflow: hidden !important;
    display: flex;
    flex-direction: column;
  }
  .el-dialog__header
  {
    text-align: center;
  }
  .el-dialog__body {
    overflow: scroll;
  }
}
</style>
