        // text: '\t10日，商务部新闻发言人高峰在发布会上表示，为进一步巩固消费持续回暖态势，今年底明年初，商务部将会同有关部门和地方，从市场体系建设、开展消费促进活动、营造良好消费环境等方面，开展一系列促消费工作。其中，扩大重点商品消费成为重点。记者获悉，新一轮汽车下乡和以旧换新正在加速开展，促进家电家具更新换代也将出台系列举措。\n' +

        //       '\t高峰介绍，在促进商品消费方面，将鼓励相关地区优化汽车限购措施，开展新一轮汽车下乡和以旧换新；落实全面取消二手车限迁政策，健全报废汽车回收利用体系；积极发展汽车养护、维修、保险、租赁等汽车后市场，畅通新车、二手车、报废汽车的流通循环。\n' +

