  },
  methods: {
    async getInfos () {
      const { data: res } = await this.$http.get('infos/detail', { params: {infoid:this.infoid} })
      if (res.meta.status !== 200) {
          return this.$message.error('数据请求失败')
      }
      this.showData = res.data
      console.log(res)
      // return this.$message.success('数据请求成功')
    },
    goClick () {
      this.$router.push({
        path:'/infoList',
        query: {
          back: true
        }
      })
    }
