<template>
  <div class="container0">
    <div class="container1">
      <div class="wrapper header-search">
        <!-- <div class="wrapper-search"> -->
        <div class="wrapper-logo" @click="goToHome">
          <i class="el-icon-s-marketing"></i>
        </div>
        <div class="wrapper-input">
          <!-- <el-input v-model="formData.phone" prefix-icon="el-icon-search"
            style="margin-top:2px" placeholder="请输入股票代码/名称/首字母">
          </el-input> -->
          <el-autocomplete style="width: 100%" :trigger-on-focus="false"
            v-model="searchSelected" placement="bottom-end"
            :fetch-suggestions="querySearch" value-key="stock_name"
            placeholder="请输入股票代码/名称/首字母" @select="handleInputSelect"
            @keyup.enter.native="submitForm('')" class="searchBar">
            <template slot-scope="{ item }">
              <el-row>
                <el-col :span="12" class="autocompleteCode">{{ item.stock_name }}</el-col>
                <el-col :span="11" class="autocompleteName">{{ item.stock_code }}</el-col>
              </el-row>
            </template>
          </el-autocomplete>
        </div>
        <!-- </div> -->
        <div class="wrapper-user">
          <el-tooltip v-model="toolTipVisible" class="item" effect="light" lacement="bottom" >
            <div class="welcomeToolTip" slot="content">
              <el-button type="danger" size="small" plain @click="toPersonalCenter()">
                {{this.buttonText}}
              </el-button><br/>
              <el-button type="danger" size="small" plain @click="signOut()">退出登录</el-button>
            </div>
            <div class="welcomeUsers">
              <el-avatar :src="require('@/assets/images/avatar.png')" :size="35"></el-avatar>
              <span>{{this.username}}</span>
            </div>
          </el-tooltip>
        </div>
      </div>
      <div height="100px" class="indexBar">
        <!-- <el-carousel trigger="click" :interval="5000">
            <el-carousel-item>
              123
            </el-carousel-item>
        </el-carousel> -->
        <el-row>
          <el-col :span="6" v-for="(item, index) in topIndexsData" :key="index">
            <div :class="['indexBox', index==4?'indexBox_last':'',
             item.up > 0 ? 'redFonts' : 'greenFonts']">
              <h1 class="indexTitle">{{item.name}}</h1>
              <p class="indexMain">{{item.indexs}}</p>
              <p class="indexDetail">
                <span v-show="item.up > 0">+</span><span style="margin-right: 0.04rem">{{item.up}}</span>
                <span v-show="item.uprang > 0">+</span><span>{{item.uprang}}%</span>
              </p>
            </div>
          </el-col>
        </el-row>
      </div>
      <!-- <div :width="asideWith" v-show="asideVisible" :style="this.asideVisible?'display:unset':'display:none'"> -->
      <div class="main-container">
        <router-view @toSimpleSearch="toSimpleSearch" :carryData="ssView"></router-view>
      </div>
      <div class="navBar">
        <!-- <div class="collpase_btn" @click="switchCollapse()">
          <i :class="isCollapse?'el-icon-d-arrow-right':'el-icon-d-arrow-left'"></i>
        </div> -->
        <el-menu background-color="#272727"
          mode="horizontal"
          text-color="#EEEEEE"
          active-text-color="#EEEEEE"
          :default-active="navPath"
          :collapse-transition="false"
          :router="true">
          <el-menu-item index="selfSelection">
            <i class="el-icon-edit"></i>
            <span>自选</span>
          </el-menu-item>
          <el-menu-item index="market">
            <i class="el-icon-goods"></i>
            <span>市场</span>
          </el-menu-item>
          <el-menu-item index="pickStocks">
            <i class="el-icon-aim"></i>
            <span>搜索</span>
          </el-menu-item>
          <!-- <el-menu-item index="infoList">
            <i class="el-icon-document"></i>
            <span>资讯</span>
          </el-menu-item> -->
          <el-menu-item index="me">
            <i class="el-icon-user"></i>
            <span>我的</span>
          </el-menu-item>
          <!-- <el-menu-item v-show="switchSSView"
            @click="changeSSView">
            <i class="el-icon-circle-close" v-if="ssView"></i>
            <span v-if="ssView">隐藏</span>
            <i class="el-icon-view" v-if="!ssView"></i>
            <span v-if="!ssView">显示</span>
          </el-menu-item> -->
        </el-menu>
        <MessageBoard class="msg-board" />
      </div>
    </div>
    <!-- <el-drawer ref='drawer' :visible.sync="drawerVisible" :direction="'ttb'" :with-header="false" modal-append-to-body="false"
      size="45" @open="pullBtnClass='pull_btn1'" @closed="pullBtnClass='pull_btn0'" style="min-width: 1515px"> -->
      
    <!-- </el-drawer> -->
