<script>
import pinyinMatch from 'pinyin-match'
import NoticeBoard from './components/NoticeBoard'
import MessageBoard from './components/MessageBoard.vue'
export default {
  components: { MessageBoard, NoticeBoard },
  created () {
    this.username = window.sessionStorage.getItem('userName')
    console.warn( window.sessionStorage.getItem('userName'))
    if (this.turnNavPath.indexOf(this.$route.path.replace('/','')) >= 0) {
      this.navPath = 'market'
    } else if (this.$route.path === '/detailInfo') {
      this.navPath = 'infoList'
    } else {
      this.navPath = this.$route.path.replace('/','')
    }
    if (window.sessionStorage.getItem('navMenuCollapse'))
      this.switchCollapse()
    if (this.$route.path === '/me') {
      this.asideVisible = false
      this.buttonText = '返回主页'
    } else {
      this.asideVisible = true
    }
    this.getInfos()
  },
  computed: {
    switchSSView() {
      var myRoute = this.$route
      return (myRoute.name === 'selfSelection') ||
        ((myRoute.name === 'pickStocks') && (myRoute.query.results != undefined))
    }
  },
  data () {
    return {
      navPath: 'market',
      turnNavPath: ['block', 'blockDetail', 'sssmarket', 'slconnect',
        'indexs', 'hkstocks', 'usstocks'],
      asideVisible: false,
      toolTipVisible: false,
      buttonText: '个人中心',
      username: '',
      dialogMarginTop: '110px',
      isCollapse: false,
      drawerVisible: true,
      pullBtnClass: 'pull_btn0',
      asideWith: '120px',
      searchSelected: '',
      queryResults: [],
      repeatQuery: 0,
      formData: {
        username: 'admin',
        verifycode: '',
        password: '123456'
      },
      rawSearchData: [],
      ssView: true,
      topIndexsData: [
        {
          name: '上证指数',
          indexs: 3597.16,
          up: 9.1,
          uprang: 9.1
        },
        {
          name: '深圳成指',
          indexs: 3597.16,
          up: 9.1,
          uprang: 9.1
        },
        {
          name: '中小板指',
          indexs: 3597.16,
          up: -9.1,
          uprang: -9.1
        },{
          name: '创业板指',
          indexs: 3597.16,
          up: -9.1,
          uprang: -9.1
        }
      ]
    }
  },
  methods: {
    switchCollapse () {
      this.isCollapse = !this.isCollapse
      this.isCollapse ? this.asideWith = '64px' : this.asideWith = '120px'
      window.sessionStorage.setItem('navMenuCollapse',this.isCollapse)
    },
    toPersonalCenter () {
      if (this.$route.path === '/me') {
        this.$router.push('/market')
        // this.asideVisible = true
        // this.buttonText = '个人中心'
        window.sessionStorage.setItem('navMenuVisible',this.asideVisible)
      } else {
        this.$router.push('/me')
        // this.asideVisible = false
        // this.buttonText = '返回主页'
      }
      this.$refs.drawer.closeDrawer()
      this.toolTipVisible = false
    },
    signOut () {
      this.$router.push('/welcome')
    },
    goToHome () {
      this.$router.push('/home')
    },
    querySearch(queryString, cb) {
      var results = queryString ? this.rawSearchData.filter(this.createFilter(queryString)) : this.rawSearchData
      // 调用 callback 返回建议列表的数据
      console.log(results)
      results.length > 50 ? cb(results.slice(0,30)) : cb(results)
      this.queryResults = results
    },
    createFilter(queryString) {
      if(isNaN(Number(queryString))) {
        return (rest) => {
          return (rest.stock_name.toLowerCase().indexOf(queryString.toLowerCase()) >= 0)
            || this.pinyinJudge(rest, queryString)
        }
      } else {
        return (rest) => {
          return (rest.stock_code.toLowerCase().indexOf(queryString.toLowerCase()) >= 0)
        }
      }
    },
    pinyinJudge(rest,queryString) {
      var pinyinJudgeResult = pinyinMatch.match(rest.stock_name,queryString)
      // if(Array.isArray(pinyinJudgeResult))
      // {
      //   console.log(rest.stock_name)
      //   console.log(pinyinJudgeResult)
      // }
      return pinyinJudgeResult[0] === 0
    },
    async loadAll() {
      const { data: res } = await this.$http.get('home/searchData')
      this.rawSearchData = res.data.details
      console.log(this.rawSearchData)
    },
    handleInputSelect(item) {
      this.searchSelected = item.stock_name
      this.submitForm(item)
    },
    toSimpleSearch(visible) {
      this.drawerVisible = visible
    },
    submitForm(v) {
      if(this.searchSelected != '') {
        v != ''
        ? window.sessionStorage.setItem('searchResult',JSON.stringify(v))
        : window.sessionStorage.setItem('searchResult',JSON.stringify(this.queryResults))
        if(this.$route.name === 'pickStocks')
        {
          this.$router.push({
            path: '/pickStocks',
            query: {
              results: true,
              repeat: this.repeatQuery++
            }
          })
        } else {
          this.$router.push({
            path: '/pickStocks',
            query: {
              results: true
            }
          })
        }
      }
    },
    async getInfos() {
      const { data: res } = await this.$http.get('home/top')
      this.topIndexsData = res.listAll
      // console.log(res)
      // return this.$message.success('数据请求成功')
    },
    changeSSView() {
      this.ssView = !this.ssView

    }
  },
  mounted() {
    this.loadAll()
  },
  watch: {
    $route (val, oldVal) {
      if (oldVal.name === 'me') {
        this.asideVisible = true
        this.buttonText = '个人中心'
      }
      if (val.name === 'me') {
        this.asideVisible = false
        this.buttonText = '返回主页'
      }
      if (this.turnNavPath.indexOf(val.name) >= 0) {
        this.navPath = 'market'
      } else if (val.name === 'detailInfo') {
        this.navPath = 'infoList'
      } else {
        this.navPath = val.name
      }
    }
  }
}
</script>

