<template>
  <div class="container-router">
    <!-- <div class="container-aside"> -->
    <div :class="['button-aside', stateOfAside ? 'button-unfold' : 'button-fold']">
      <el-button round @click="handleClickAside">
        <!-- <i :class="[stateOfAside ? 'el-icon-s-fold' : 'el-icon-s-unfold']"></i> -->
        <i class="el-icon-more"></i>
      </el-button>
    </div>
    <div :class="['container-aside', stateOfAside ? 'aside-visible' : 'aside-hidden']">
      <div class="grouping">
        <el-button style="width:35%;border-radius: 4px 0 0 4px;margin-right:0px"
          type="warning" class="grouping" @click="toPickStocks">选股</el-button>
        <el-dropdown placement="right" trigger="click" style="width:35%" @command="onDropDownClick">
          <el-button style="width:100%;border-radius: 0 4px 4px 0;margin-left:0px"
            type="danger" class="grouping">{{this.dropDownShow}}</el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-for="item in 5" :key="item" :divided="item != 1" :command="item">
              {{'自选股-分组'+item}}
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <div class="wrapper-table">
        <el-table :data="tableData" ref="asideTableRef" @row-click="onAsideTableClick"
          @expand-change="onAsideTableExpandChange" :row-key="getRowKeys"
          :expand-row-keys="asideTableExpendPowKey" highlight-current-row
          style="width: 98%" :cell-style="()=>{return 'background-color:#f2f2f2;text-align: center'}"
          :header-cell-style="()=>{return 'background-color:#e1e1e1;text-align: center'}">
          <el-table-column label="股票">
            <template slot-scope="scope">
              <div class="tableItemTitle">{{scope.row.name}}</div>
              <!-- style="font-size: 13px;" -->
              <div class="tableItemTitle">{{scope.row.stock_code}}</div>
            </template>
          </el-table-column>
          <el-table-column label="最新" prop="last">
          </el-table-column>
          <el-table-column label="涨幅">
            <template slot-scope="scope">
              <el-tag effect="dark" :type="scope.row.amount>0?'danger':'success'" size="small">
                <span v-if="scope.row.amount>0">+</span>
                <span>{{scope.row.amount}}%</span>
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column label="查看" :width="65">
            <template>
              <el-button icon="el-icon-arrow-right" type="info" plain circle size="mini" @click="handleClickAside"></el-button>
            </template>
          </el-table-column>
          <el-table-column type="expand" :width="1">
            <template :slot-scope="scope">
              <el-row>
                <el-col :span="12">五档</el-col>
                <el-col :span="12">大单</el-col>
              </el-row>
              <SSDetailList :stockid="selectedStockid"></SSDetailList>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <el-main>
      <div>
        <div class="header">
          <el-tabs v-model="topActiveName" class="topTabs">
            <el-tab-pane label="K线图" name="kchart" style="height:500px">
              <KCharts v-if="topActiveName==='kchart'" :toReSize="carryData" :stockId="selectedStockid"
                style="position: relative; left: -21px" :detailParams="formData"></KCharts>
            </el-tab-pane>
            <el-tab-pane label="分时图" name="timeline" style="height:500px">
              <live-chart></live-chart>
            </el-tab-pane>
          </el-tabs>
          <el-button type="info" plain @click="toMore" size="mini" style="position: absolute; right: 8px; top: 64px; width: 88px; z-index: 8">更多设置</el-button>
        </div>
        <div>
          <!-- <el-menu :default-active="'funds'" mode="horizontal"
            background-color="#f15151" text-color="#f4AAAA"
            active-text-color="#ffffff" @select="onSelected">
            <el-menu-item index="funds">资金</el-menu-item>
            <el-menu-item index="infos">资讯</el-menu-item>
            <el-menu-item index="f10">F10</el-menu-item>
          </el-menu> -->
          <div class="main-header-charts-tab-box">
            <macd-kdj-tabs class="topTabs h-full" :macdData="macdData"></macd-kdj-tabs>
          </div>
          <el-tabs v-model="activeName" class="bottomTabs">
            <el-tab-pane label="资金" name="first" style="height:1110px">
              <SSFundInfo2 v-if="activeName==='first'" :stockId="selectedStockid"
                :toReSize="carryData" style="margin: 0 15px 0 15px"></SSFundInfo2>
            </el-tab-pane>
            <el-tab-pane label="资讯" name="second">
              <SSInfo v-if="activeName==='second'" :stockId="selectedStockid"></SSInfo>
            </el-tab-pane>
            <el-tab-pane label="大事提醒" name="third">
              <SSEvents v-if="activeName==='third'" :stockId="selectedStockid"></SSEvents>
            </el-tab-pane>
            <el-tab-pane label="主营构成" name="fourth">
              <SSComposition v-if="activeName==='fourth'" :stockId="selectedStockid"></SSComposition>
            </el-tab-pane>
            <el-tab-pane label="股本结构" name="fifth">
              <SSStructure v-if="activeName==='fifth'" :stockId="selectedStockid"></SSStructure>
            </el-tab-pane>
            <el-tab-pane name="sixth">
              <span slot="label">
                <span>财务分析</span>
              </span>
              <SSAnalyse v-if="activeName==='sixth'" :stockId="selectedStockid"></SSAnalyse>
            </el-tab-pane>
            <!-- <el-tab-pane label="TEST" name="seventh">
              <SSFundsInfo></SSFundsInfo>
            </el-tab-pane> -->
          </el-tabs>
        </div>
      </div>
    </el-main>
    <!-- <el-dialog :visible.sync="dialogVisible" width="650px"> -->
    <el-dialog :visible.sync="dialogVisible" fullscreen>
      <el-form :model="formData" label-position="top">
        <el-form-item label="时间区间">
          <el-date-picker
            v-model="formData.pickTime"
            type="daterange"
            align="right"
            style="width: 100%"
            unlink-panels
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            value-format="yyyy-MM-dd"
            :picker-options="pickerOptions">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="年|周|日K线">
          <!-- <el-select v-model="formData.elSelectValue" placeholder="请选择" style="width:350px"> -->
          <el-select v-model="formData.elSelectValue" placeholder="请选择" style="width: 100%">
            <el-option
              v-for="item in this.formElSelectOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value"
              :disabled="item.disabled"
              style="text-align:center">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="K线图指数">
          <el-row style="padding-top: 9px;">
            <el-col :span="12" style="border-top:0px">
              <el-switch
                style="display: block"
                v-model="formData.ma"
                active-color="#b368ff"
                active-text="MA指数"
                @change="onSwitchChange('ma')">
              </el-switch>
            </el-col>
            <el-col :span="12" style="border-top:0px">
              <el-switch
                style="display: block"
                v-model="formData.boll"
                @change="onSwitchChange('boll')"
                active-color="#b368ff"
                active-text="BOLL指数">
              </el-switch>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="其他指数">
          <el-row style="padding-top: 9px">
            <el-col :span="12" style="border-top:0px">
              <el-switch
                style="display: block"
                v-model="formData.kdj"
                active-color="#b368ff"
                active-text="KDJ指数"
                @change="onSwitchChange('ma')">
              </el-switch>
            </el-col>
            <el-col :span="12" style="border-top:0px">
              <el-switch
                style="display: block"
                v-model="formData.macd"
                @change="onSwitchChange('boll')"
                active-color="#b368ff"
                active-text="MACD指数">
              </el-switch>
            </el-col>
          </el-row>
        </el-form-item>
        <el-form-item label="MA参数①">
          <el-input v-model="formData.maParam[0]" onkeyup="this.value=this.value.replace(/[^\d.]/g,'');" maxlength="2"></el-input>
        </el-form-item>
        <el-form-item label="MA参数②">
          <el-input v-model="formData.maParam[1]" onkeyup="this.value=this.value.replace(/[^\d.]/g,'');" maxlength="2"></el-input>
        </el-form-item>
        <el-form-item label="MA参数③">
          <el-input v-model="formData.maParam[2]" onkeyup="this.value=this.value.replace(/[^\d.]/g,'');" maxlength="2"></el-input>
        </el-form-item>
        <el-form-item label="BOLL参数">
          <el-input v-model="formData.bollParam" onkeyup="this.value=this.value.replace(/[^\d.]/g,'');" maxlength="2"></el-input>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  import _ from 'lodash'
  import SSDetailList from './DetailList.vue'
  import SSFundsInfo from './FundsInfo.vue'
  import SSInfo from './Infos.vue'
  import SSEvents from './Events.vue'
  import SSComposition from './Composition.vue'
  import SSStructure from './Structure.vue'
  import SSAnalyse from './Analyse.vue'
  import SSFundInfo2 from './FundsInfo2.vue'
  import KCharts from './KCharts.vue'
  import LiveChart from './LiveChart.vue'
  import MacdKdjTabs from './components/MacdKdjTabs'
  export default {
    components: { SSDetailList, SSFundsInfo, SSInfo, SSEvents, SSComposition, SSStructure, SSAnalyse, SSFundInfo2, KCharts, LiveChart, MacdKdjTabs},
    props: ['carryData'],
    created () {
      this.getInfos().then((result) => {
        this.selectedStockid = this.tableData[0].stock_code
        this.id.id=this.selectedStockid
        this.getmacd()
      })
    },
    mounted () {
      window.addEventListener('keydown', this.handleKeyPress);
    },
    beforeDestroy () {
      window.removeEventListener('keydown', this.handleKeyPress);
    },
    computed: {
      toMAorBOLL() {
        this.formData.maORboll = !this.formData.maORboll
        return this.formData.maORboll
      }
    },
    data () {

      return {
        activeName: 'first',
        topActiveName: 'kchart',
        selectedStockid: '',
        dropDownShow: '自选股-分组1',
        dialogVisible: false,
        asideTableExpendPowKey: [],
        pickerOptions: {
          shortcuts: [{
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        formData: {
          pickTime: [ "2020-11-03", "2021-02-01" ],
          maParam: [3, 5, 10],
          bollParam: 20,
          ma: true,
          boll: false,
          kdj: false,
          macd: false,
          elSelectValue: '2'
        },
        formElSelectOptions: [{
          value: '0',
          label: '年K线'
        }, {
          value: '1',
          label: '周K线'
        }, {
          value: '100',
          label: '月K线',
          disabled: true
        }, {
          value: '2',
          label: '日K线'
        }],
        formDataRules: {
            ma: [
                { required: true, message: '请输入参数', trigger: 'blur' },
                { type:'number', message: '请输入数字', trigger: ['blur', 'change'], transform: (value) => Number(value)}
            ]
        },
        tableData: [
          {
            stock_code: 'SZ399001',
            name: '深证成指',
            last: 10033.36,
            amount: '0.04'
          },
          {
            stock_code: 'SZ399002',
            name: '深证成指',
            last: 10033.36,
            amount: '0.04'
          },
          {
            stock_code: 'SZ399003',
            name: '深证成指',
            last: 10033.36,
            amount: '0.04'
          }
        ],
        queryInfo: {
          user_id: 2,
          group_id: 1
        },
        id:{
          id: ''
        },
        macdData:[],
        stateOfAside: true
      }
    },
    methods: {
      async getInfos () {
        const { data: res } = await this.$http.get('selfselection/aside/total', { params: this.queryInfo })
        if (res.meta.status !== 200) {
            return this.$message.error('数据请求失败')
        }
        this.tableData = res.data.details
      },


    //test
    async getmacd () {
        const { data: res } = await this.$http.get('macd/macdInfo', { params: this.id })
        if (res.meta.status !== 200) {
            return this.$message.error('数据请求失败')
        }
        this.macdData = res.data.maCDInfo
        console.log(this.macdData)
      },

      onAsideTableClick (row, column, event) {
        this.$refs.asideTableRef.toggleRowExpansion(row)
      },
      onAsideTableExpandChange (row, expandedRows) {
        this.asideTableExpendPowKey = []
        if (expandedRows.length > 0) {
          if (row) {
            this.asideTableExpendPowKey.push(row.stock_code)
          }
        this.selectedStockid = row.stock_code
       }
      },
      getRowKeys (row) {
        return row.stock_code
      },
      onSelected (key, keyPath) {
        for (var item in this.visible) {
          if (this.visible[item]) {
            this.visible[item] = false
            // console.log(this.visible[item])
          }
        }
        this.visible[key] = true
      },
      toPickStocks () {
        this.$router.push('/pickStocks')
      },
      onDropDownClick (v) {
        this.queryInfo.group_id = v
        this.dropDownShow = '自选股-分组' + v
        this.getInfos()
      },
      toMore () {
        this.dialogVisible = !this.dialogVisible
      },
      onSwitchChange(v) {
        v === 'ma' ? this.formData.boll = !this.formData.ma : this.formData.ma = !this.formData.boll
      },
      handleKeyPress: _.debounce(function (e) {
        if (e.keyCode === 121 && this.activeName) {
          this.activeName = 'sixth';
        }
      }, 300),
      handleClickAside() {
        this.stateOfAside = !this.stateOfAside;
      }
    }
  }
</script>

<style lang="less" scoped>
.container-router {
  width: 100%;
  height: 100%;
  font-size: 16px;
  overflow: hidden scroll;
}
.el-collapse
{
  width: 240px !important;
}
.el-container
{
  min-height: 100%;
}
.el-aside
{
  background-color: #f2f2f2;
  border-right:3px solid #ffffff;
}
.el-main {
  padding: 0;
}
.header
{
  position: relative;
  height: 500px;
  padding: 0;
}
.el-table
{
  background-color: #f2f2f2;
  color: #5A5A5A;
  left: 5px;
}
.el-col
{
  text-align: center;
  font-size: 12px;
  border-top: 2px solid #f1f1f1;
}
.el-tabs__item
{
  color: #f1f1f1;
}
.tableItemTitle
{
  // line-height: 0px;
}
.grouping
{
  margin: 3px 2px;
}
.el-tag--dark
{
  width:125%;
  text-align: center;
  position: relative;
  left: -5px;
}
.el-form-item
{
  // margin-left: 81px;
}
::v-deep
{
  .el-collapse-item__content
  {
    background-color: #f5f5f5;
  }
  .el-table__body tr.current-row > td
  {
    background-color: #ffffff !important;
  }
  .el-table td
  {
    border-bottom: 0px;
    padding: 6px 0px;
  }
  .el-table__expanded-cell
  {
    padding: 0px !important;
  }
  .bottomTabs
  {
    .el-tabs__header
    {
      background-color: #f15151;
      margin: 24px 0;
    }
    .el-tabs__item.is-active
    {
      color: #ffffff;
    }
    .el-tabs__item.is-active:hover
    {
      color: #ffffff;
    }
    .el-tabs__active-bar
    {
      background: none;
    }
    .el-tabs__item:hover
    {
      background-color: #df2121;
      color: #f5a3a3
    }
    .el-tabs__item
    {
      color: #f5a3a3
    }
    .el-tabs__nav-scroll {
      overflow: scroll;
    }
  }
  .topTabs
  {
    .el-tabs__item.is-active
    {
      color: #ff6969;
    }
    .el-tabs__item.is-active:hover
    {
      color: #ff6969
    }
    .el-tabs__item:hover
    {
      color: #ff6969
    }
    .el-tabs__active-bar
    {
      background: #ff6969;
    }
  }
  .el-tabs__header
  {
    margin-bottom: 0px
  }
  .el-tabs--top .el-tabs__item.is-top:nth-child(2)
  {
    padding-left: 20px
  }
  .el-tabs--top .el-tabs__item.is-top:last-child
  {
    padding-right: 20px;
  }
  .el-dropdown-menu__item:focus, .el-dropdown-menu__item:not(.is-disabled):hover
  {
    background-color: #ffecec;
    color: #ff6666;
  }
  .el-select .el-input__inner
  {
    text-align: center;
  }
}

.main-header-charts-tab-box {
  height: 280px
}
</style>

<style>
.el-picker-panel
{
  left: 25% !important
}
.el-form-item__label
{
  width: 85px;
  text-align: left !important;
}
.corner-mark {
  position: absolute !important;
  transform: translate(-4px, -4px);
}
</style>

<style lang="less" scoped>
.el-tabs {
  font-size: 14px;
}
.grouping {
  font-size: 14px;
}
.container-aside {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  box-sizing: border-box;
  overflow-y: scroll;
  background-color: #F2F2F2;
  z-index: 9;
  transition: all 0.3s ease-out;
}
.button-aside {
  position: absolute;
  top: 56px;
  // left: -16px;
  z-index: 8;
  font-size: 12px;
  transform: translate(-16px);
  transition: all 0.3s ease-out;

  .el-button {
    color: #606266 !important;
    background: rgb(225, 225, 225) !important;
  }

  .el-button.is-round {
    padding-right: 12px !important;
  }
}
.button-fold {
  left: 0px;
}
.button-unfold {
  left: 100%;
}
