<template>
  <div class="Echarts">
    <div id="main" ref="kChart" style="width: 100%;height:460px;"></div>
  </div>
</template>

<script>
var upColor = '#ec0000';
var downColor = '#00da3c';
export default {
  name: 'KCharts',
  props: ['toReSize', 'detailParams', 'stockId'],
  created() {
  },
  data() {
    this.myChart = ''
    this.lineColor = ['#c23531','#2f4554', '#61a0a8']
    return {
      datas: [],
      modifiedDatas:[],
      lineColor: ['#c23531','#2f4554', '#61a0a8'],
      kChartQueryInfo: {
        stock_code: '',
        startTime: '2020-01-26',
        // startTime: '1990-12-10',
        endTime: '2021-01-26',
        interval: 2
      },
      bollSubdivide: ['Up', 'MB', 'Down'],
      option: {
        backgroundColor: '#fff',
        animation: true,
        legend: {
          top: 10,
          left: 'center',
          data: ['K线',
            'MA'+this.detailParams.maParam[0],
            'MA'+this.detailParams.maParam[1],
            'MA'+this.detailParams.maParam[2]]
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
        },
          backgroundColor: 'rgba(245, 245, 245, 0.8)',
          borderWidth: 1,
          borderColor: '#ccc',
          padding: 10,
          textStyle: {
            color: '#000'
        },
        position: function (pos, params, el, elRect, size) {
          var obj = {top: 45};
          obj[['left', 'right'][+(pos[0] < size.viewSize[0] / 2)]] = 30;
          return obj;
        },
        formatter: (param) => {
          if(param[0].componentSubType == 'candlestick') {
            return [
              '时间: ' + param[0].name + '<hr size=1 style="margin: 5px 0">',
              '<span style="color:'+param[0].color+';font-size:19px;margin-right: 2px">●</span>'+'K线图 :<br/>',
              '<span style="color:#ec0000;font-size:13px;margin-left: 2px">● </span>'+'开盘价 : ' + param[0].data[1] + '<br/>',
              '<span style="color:#00da3c;font-size:13px;margin-left: 2px">● </span>'+'收盘价 : ' + param[0].data[2] + '<br/>',
              '<span style="color:#ec0000;font-size:13px;margin-left: 2px">● </span>'+'最低价 : ' + param[0].data[3] + '<br/>',
              '<span style="color:#00da3c;font-size:13px;margin-left: 2px">● </span>'+'最高价 : ' + param[0].data[4] + '<br/>',
              '<span style="color:##b368ff;font-size:13px;margin-left: 2px">● </span>'+'涨跌幅 : ' + (param[0].data[5]).toFixed(3) + '%<br/>',
              '<span style="color:'+this.lineColor[2]+';font-size:19px;margin-right: 2px">●</span>'+'成交量 : ' + param[0].data[6] + '<br/>',
              '<span style="color:'+this.lineColor[0]+';font-size:19px;margin-right: 2px">●</span>'+'MA'+this.detailParams.maParam[0]+' : ' + param[1].data + '<br/>',
              '<span style="color:'+this.lineColor[1]+';font-size:19px;margin-right: 2px">●</span>'+'MA'+this.detailParams.maParam[1]+' : ' + param[2].data + '<br/>',
              '<span style="color:'+this.lineColor[2]+';font-size:19px;margin-right: 2px">●</span>'+'MA'+this.detailParams.maParam[2]+' : ' + param[3].data + '<br/>',
            ].join('');
          } else {
            return [
              '时间: ' + param[1].name + '<hr size=1 style="margin: 5px 0">',
              '<span style="color:'+param[1].color+';font-size:19px;margin-right: 2px">●</span>'+'K线图 :<br/>',
              '<span style="color:#ec0000;font-size:13px;margin-left: 2px">● </span>'+'开盘价 : ' + param[1].data[1] + '<br/>',
              '<span style="color:#00da3c;font-size:13px;margin-left: 2px">● </span>'+'收盘价 : ' + param[1].data[2] + '<br/>',
              '<span style="color:#ec0000;font-size:13px;margin-left: 2px">● </span>'+'最低价 : ' + param[1].data[3] + '<br/>',
              '<span style="color:#00da3c;font-size:13px;margin-left: 2px">● </span>'+'最高价 : ' + param[1].data[4] + '<br/>',
              '<span style="color:##b368ff;font-size:13px;margin-left: 2px">● </span>'+'涨跌幅 : ' + (param[1].data[5]).toFixed(3) + '%<br/>',
              '<span style="color:'+this.lineColor[2]+';font-size:19px;margin-right: 2px">●</span>'+'成交量 : ' + param[1].data[6] + '<br/>',
              '<span style="color:'+this.lineColor[0]+';font-size:19px;margin-right: 2px">●</span>'+'MA'+this.detailParams.maParam[0]+' : ' + param[2].data + '<br/>',
              '<span style="color:'+this.lineColor[1]+';font-size:19px;margin-right: 2px">●</span>'+'MA'+this.detailParams.maParam[1]+' : ' + param[3].data + '<br/>',
              '<span style="color:'+this.lineColor[2]+';font-size:19px;margin-right: 2px">●</span>'+'MA'+this.detailParams.maParam[2]+' : ' + param[4].data + '<br/>',
            ].join('');
          }
          
        }
        // extraCssText: 'width: 170px'
        },
        axisPointer: {
        link: {xAxisIndex: 'all'},
        label: {
          backgroundColor: '#777'
        }
        },
        toolbox: {
        right: 25,
        feature: {
          dataZoom: {
          yAxisIndex: false
          },
          brush: {
          type: ['lineX', 'clear']
          }
        }
        },
        brush: {
        xAxisIndex: 'all',
        brushLink: 'all',
        outOfBrush: {
          colorAlpha: 0.1
        }
        },
        visualMap: {
        show: false,
        seriesIndex: 4,
        dimension: 2,
        pieces: [{
          value: 1,
          color: downColor
        }, {
          value: -1,
          color: upColor
        }]
        },
        grid: [
        {
          left: '13.5%',
          right: '7.5%',
          height: '50%'
        },
        {
          left: '13.5%',
          right: '7.5%',
          top: '67%',
          height: '16%'
        }
        ],
        xAxis: [
        {
          type: 'category',
          data: '',
          scale: true,
          boundaryGap: false,
          axisLine: {onZero: false},
          splitLine: {show: false},
          splitNumber: 20,
          min: 'dataMin',
          max: 'dataMax',
          axisPointer: {
          z: 100
          }
        },
        {
          type: 'category',
          gridIndex: 1,
          data: '',
          scale: true,
          boundaryGap: false,
          axisLine: {onZero: false},
          axisTick: {show: false},
          splitLine: {show: false},
          axisLabel: {show: false},
          splitNumber: 20,
          min: 'dataMin',
          max: 'dataMax'
        }
        ],
        yAxis: [
        {
          scale: true,
          splitArea: {
          show: true
          }
        },
        {
          scale: true,
          gridIndex: 1,
          splitNumber: 2,
          axisLabel: {show: false},
          axisLine: {show: false},
          axisTick: {show: false},
          splitLine: {show: false}
        }
        ],
        dataZoom: [
        {
          type: 'inside',
          xAxisIndex: [0, 1],
          start: 50,
          end: 100,
          minSpan: 10
        },
        {
          show: true,
          xAxisIndex: [0, 1],
          type: 'slider',
          top: '85%'
        }
        ],
        series: [
          {
            name: 'K线',
            type: 'candlestick',
            data: '',
            itemStyle: {
              color: upColor,
              color0: downColor,
              borderColor: null,
              borderColor0: null
            },
            tooltip: {
            },
            markPoint: {
              // label: {
              //   normal: {
              //     formatter: function (param) {
              //         return param != null ? Math.round(param.value) : '';
              //     }
              //   }
              // },
              data: [
                {
                  name: 'highest value',
                  type: 'max',
                  valueDim: 'highest'
                },
                {
                  name: 'lowest value',
                  type: 'min',
                  valueDim: 'lowest',
                  symbolRotate: 180,
                  itemStyle: {
                    color: '#00da3c'
                  },
                  label:{
                    offset:[0,10]
                  }
                }
              ],
              tooltip: {
                formatter: function (param) {
                  return param.name + '<br>' + (param.data.coord || '');
                }
              }
            },
          },
          {
            name: 'MA'+ this.detailParams.maParam[0],
            type: 'line',
            symbol: 'none',
            data: '',
            smooth: true,
            lineStyle: {
              width: 2,
              color: this.lineColor[0],
              opacity: 0.5
            }
          },
          {
            name: 'MA'+ this.detailParams.maParam[1],
            type: 'line',
            symbol: 'none',
            data: '',
            smooth: true,
            lineStyle: {
              width: 2,
              color: this.lineColor[1],
              opacity: 0.5
            }
          },
          {
            name: 'MA'+ this.detailParams.maParam[2],
            type: 'line',
            symbol: 'none',
            data: '',
            smooth: true,
            lineStyle: {
              width: 2,
              color: this.lineColor[2],
              opacity: 0.5
            }
          },
          {
            name: 'Volume',
            type: 'bar',
            xAxisIndex: 1,
            yAxisIndex: 1,
            itemStyle: {
                color: upColor,
                color0: downColor,
                borderColor: null,
                borderColor0: null
            },
            data: ''
          }
        ]
      }
    }
  },
  methods:{
    splitData(rawData) {
      var categoryData = [];
      var values = [];
      var volumes = [];
      for (var i = 0; i < rawData.length; i++) {
        categoryData.push(rawData[i].splice(0, 1)[0]);
        values.push(rawData[i]);
        volumes.push([i, rawData[i][5], rawData[i][0] > rawData[i][1] ? 1 : -1]);
      }  
      return {
        categoryData: categoryData,
        values: values,
        volumes: volumes
      };
    },
    calculateMA(dayCount, data) {
      var result = [];
      for (var i = 0, len = data.values.length; i < len; i++) {
        if (i < dayCount) {
          result.push('-');
          continue;
        }
        var sum = 0;
        for (var j = 0; j < dayCount; j++) {
          sum += data.values[i - j][1];
        }
        result.push(+(sum / dayCount).toFixed(3));
      }
      return result;
    },
    calculateBoll(dayCount, data, whichOne) {
    var result = []
    var MA = this.calculateMA(dayCount, data)
    var sqrtArray = []
    var temp = 0
    for (var i = 0, len = data.categoryData.length; i < len; i++) {
        if (i < dayCount) {
            sqrtArray.push('-')
            continue;
        }
        for (var j = 0; j < dayCount; j++) {
            temp += Math.pow(data.values[i - j][1] - MA[i],2);
        }
        sqrtArray.push(+(Math.sqrt(temp / dayCount)).toFixed(3))
        temp = 0
    }
    switch (whichOne) {
        case 0:
          for (var i = 0, len = data.categoryData.length; i < len; i++) {
            if (i < dayCount) {
              result.push('-')
              continue
            }
            result.push(+(MA[i]+2*sqrtArray[i]).toFixed(3))
          }
          return result
        case 1:
          return MA
        case 2:
          for (var i = 0, len = data.categoryData.length; i < len; i++) {
              if (i < dayCount) {
                  result.push('-')
                  continue
              }
              result.push(+(MA[i]-2*sqrtArray[i]).toFixed(3))
          }
          return result
        default:
          console.log("Method 'calculateBoll()' Error")
          return
    }
    },
    myEcharts(v) {
      this.modifiedDatas = this.splitData(this.datas)
      for (var index = 0; index < this.option.xAxis.length; index++) {
        this.option.xAxis[index].data = this.modifiedDatas.categoryData
      }
      this.option.series[0].data = this.modifiedDatas.values
      if(v) {
        for (var index = 1; index < 4; index++) {
          this.option.series[index].data = this.calculateMA(this.detailParams.maParam[index - 1], this.modifiedDatas)
        }
      } else {
        this.option.legend.data = ['K线']
        for (var index = 1; index < 4; index++) {
          this.option.series[index].data = this.calculateBoll(this.detailParams.bollParam,this.modifiedDatas,index - 1)
          this.option.legend.data.push('Boll-' + this.bollSubdivide[index - 1])
          this.option.series[index].name = 'Boll-' + this.bollSubdivide[index - 1]
        }
      }
      this.option.series[this.option.series.length - 1].data = this.modifiedDatas.volumes
      this.myChart.setOption(this.option)
    },
    async getKChartData() {
      this.myChart.clear()
      this.myChart.showLoading()
      const { data: res } = await this.$http.get('history/listAll', { params: this.kChartQueryInfo })
      this.datas = res.data.details
      this.myEcharts(this.detailParams.ma)
      this.myChart.hideLoading();
    },
    onMAParamChanged(v) {
      this.option.legend.data = ['K线']
      for (var index = 1; index < 4; index++) {
        this.option.series[index].data = this.calculateMA(v[index - 1], this.modifiedDatas)
        this.option.series[index].name = 'MA' + v[index - 1]
        this.option.legend.data.push('MA' + v[index - 1])
      }
    },
    onBollParamChanged(v) {
      this.option.legend.data = ['K线']
      for (var index = 1; index < 4; index++) {
        this.option.series[index].data = this.calculateBoll(v,this.modifiedDatas,index - 1)
        this.option.series[index].name = 'Boll-' + this.bollSubdivide[index - 1]
        this.option.legend.data.push('Boll-' + this.bollSubdivide[index - 1])
      }
    }
  },
  mounted() {
    this.myChart = this.$echarts.init(document.getElementById('main'))
    this.myChart.showLoading()
    // // https://echarts.apache.org/examples/zh/editor.html?c=dynamic-data
    // //设置间隔1秒的定时器
    // window.chatTimer = setInterval(()=>{
    //   //获取数据
    //   const { data: res } = await this.$http.get('history/listAll', { params: this.kChartQueryInfo })
    //   //将获取到的数据分别加入this.option下各项需要配置数据（data）的地方
    //   this.option.xAxis[0].data.push(res.categoryData)
    //   this.option.xAxis[1].data.push(res.categoryData)
    //   this.option.series[0].data.push(res.series0)
    //   this.option.series[1].data.push(res.series1)
    //   //实现更新
    //   myChart.setOption(this.option);
    // }, 1000);
    document.onkeydown = (e) => {
      // console.log('qwe')
      var CurrentDataZoom = this.myChart.getOption().dataZoom[0]
      //事件对象兼容
      let e1 = e || event || window.event || arguments.callee.caller.arguments[0]
      //键盘按键判断:左箭头-37;上箭头-38；右箭头-39;下箭头-40
      if (e1 && e1.keyCode == 38) {
        CurrentDataZoom.start >= 2.5
        ?this.option.dataZoom[0].start = CurrentDataZoom.start - 2.5
        :this.option.dataZoom[0].start = 0
        CurrentDataZoom.end <= 100
        ?this.option.dataZoom[0].end = CurrentDataZoom.end + 2.5
        :this.option.dataZoom[0].end = 100
        this.myChart.setOption(this.option)
        e1.preventDefault()
      } else if (e1 && e1.keyCode == 40) {
        var centerPoint = (CurrentDataZoom.start + CurrentDataZoom.end) / 2
        CurrentDataZoom.start <= centerPoint - 5
        ?this.option.dataZoom[0].start = CurrentDataZoom.start + 2.5
        :this.option.dataZoom[0].start = centerPoint - 5
        CurrentDataZoom.end >= centerPoint + 5
        ?this.option.dataZoom[0].end = CurrentDataZoom.end - 2.5
        :this.option.dataZoom[0].end = centerPoint + 5
        this.myChart.setOption(this.option)
        e1.preventDefault()
      } else if(e1 && e1.keyCode == 37) {
        if(CurrentDataZoom.start >= 0) {
          this.option.dataZoom[0].start = CurrentDataZoom.start - 2.5
          this.option.dataZoom[0].end = CurrentDataZoom.end - 2.5
        } else {
          this.option.dataZoom[0].end = CurrentDataZoom.end - CurrentDataZoom.start
          this.option.dataZoom[0].start = 0
        }
        this.myChart.setOption(this.option)
        e1.preventDefault()
      } else if(e1 && e1.keyCode == 39) {
        if(CurrentDataZoom.end <= 100) {
          this.option.dataZoom[0].start = CurrentDataZoom.start + 2.5
          this.option.dataZoom[0].end = CurrentDataZoom.end + 2.5
        } else {
          this.option.dataZoom[0].start = CurrentDataZoom.start + (100 - CurrentDataZoom.end)
          this.option.dataZoom[0].end = 100
        }
        this.myChart.setOption(this.option)
        e1.preventDefault()
      }
    }

  },
  beforeDestroy() {
    document.onkeydown = ''
    // //记得清除定时器
    // clearInterval(window.chatTimer);
  },
  watch: {
    toReSize() {
      // this.$echarts.getInstanceByDom(document.getElementById('main')).resize()
      this.myChart.resize()
    },
    "detailParams.maParam"(v) {
      this.onMAParamChanged(v)
      this.myChart.setOption(this.option)
    },
    "detailParams.bollParam"(v) {
      this.onBollParamChanged(v) 
      this.myChart.setOption(this.option) 
    },
    "detailParams.ma"(v) {
      v ? this.onMAParamChanged(this.detailParams.maParam) 
      : this.onBollParamChanged(this.detailParams.bollParam) 
      this.myChart.setOption(this.option)
    },
    "detailParams.elSelectValue"(v) {
      this.kChartQueryInfo.interval = v
      this.getKChartData()
    },
    "detailParams.pickTime"(v) {
      this.kChartQueryInfo.startTime = v[0]
      this.kChartQueryInfo.endTime = v[1]
      this.getKChartData()
    },
    stockId(v) {
      this.kChartQueryInfo.stock_code = v
      this.getKChartData()
    }
  }
}
</script>

