<template>
  <div class="container">
    <el-radio-group v-model="queryInfo.tablename" fill="#f04844" @change="radioGroupChange">
      <el-radio-button label="沪伦通GDR"></el-radio-button>
      <el-radio-button label="沪伦通CDR"></el-radio-button>
    </el-radio-group>
    <el-table :data="tableData" border height="443"
      :cell-style="()=>{return 'text-align: center'}"
      :header-cell-style="()=>{return 'background:#fef1f0;text-align: center'}">
      <el-table-column type="index" label="#" width="39px" fixed="left">
      </el-table-column>
      <el-table-column prop="name" label="名称" fixed="left">
      </el-table-column>
      <el-table-column prop="last" label="最新">
      </el-table-column>
      <el-table-column prop="uprange" label="涨幅">
      </el-table-column>
      <el-table-column prop="charge" label="涨跌">
      </el-table-column>
      <el-table-column prop="prevclose" label="昨收">
      </el-table-column>
      <el-table-column prop="volume" label="成交量">
      </el-table-column>
      <el-table-column prop="turnover" label="成交额">
      </el-table-column>
      <el-table-column prop="max" label="最高">
      </el-table-column>
      <el-table-column prop="min" label="最低">
      </el-table-column>
    </el-table>
    <!-- @size-change="handleSizeChange" @current-change="handleCurrentChange" -->
    <el-pagination :current-page="this.queryInfo.pagenum"
      :page-sizes="[1, 2, 5, 10, 20, 100, 200]"
      :page-size="queryInfo.pagesize"
      :total="this.total" background
      layout="total, sizes, prev, pager, next, jumper"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange">
    </el-pagination>
    <!-- <p v-for="i in 15" :key="i">1</p> -->
  </div>
</template>

<script>
export default {
  created () {
    this.getTableData()
  },
  data () {
    return {
      userid: 0,
      queryInfo: {
        tablename: '沪伦通GDR',
        pagenum: 1,
        pagesize: 1
      },
      total: 1,
      tableData: [
        {
          // name: 'NULL',
          // last: 0,
          // uprange: 0,
          // charge: 0,
          // prevclose: 0,
          // volume: 0,
          // turnover: 0,
          // max: 0,
          // min: 0,
          // amplitude: 0,
          // accelerate: 0
        }
      ]
    }
  },
  methods: {
    async getTableData () {
      const { data: res } = await this.$http.get('market/slconnect', { params: this.queryInfo })
      if (res.meta.status !== 200) {
          return this.$message.error('数据请求失败')
      }
      this.tableData = res.data.details
      this.total = res.data.totalpage
      // console.log('请求参数')
      // console.log(this.queryInfo)
      // console.log('数据')
      console.log(res)
      // return this.$message.success('数据请求成功')
    },
    handleSizeChange (val) {
      this.queryInfo.pagesize = val
      this.getTableData()
      // console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
      this.queryInfo.pagenum = val
      this.getTableData()
      // console.log(`当前页: ${val}`)
    },
    radioGroupChange (v) {
      this.getTableData() 
    }
  }
}
</script>

<style lang="less" scoped>
.container
{
  background: #FFFFFF;
  padding: 0 15px 0 15px;
  border-top: 10px solid #eeeeee !important;
}
.titleBar
{
  width: 750px;
  padding: 7px 0 7px 10px;
}
.el-radio-group
{
  margin: 5px 0px 5px 0px;
}
.el-radio-button
{
  margin-right: 25px;
}
.el-pagination
{
  margin: 10px 0 10px 0;
}
::v-deep
{
  .el-pagination.is-background .el-pager li:not(.disabled).active
  {
    background-color: #f14747;   // 进行修改选中项背景和字体
    color: #EEEEEE;
  }
  .el-pagination.is-background .el-pager li:not(.active):hover
  {
    color:#ff4040;
  }
  .el-radio-button__inner:hover
  {
    color: #f56c6c;
  }
  .el-radio-button__inner
  {
    border-radius:4px 4px 4px 4px !important;
    border:1px solid #fccece;
    background: #FFFFFF;
    color: #f56c6c;
    width: 125px;
  }
}
</style>
