<template>
  <div>
    <ul class="infinite-list" v-infinite-scroll="getInfos" style="overflow:auto;" infinite-scroll-disabled="disabled">
      <li v-for="i in count" :key="i" class="infinite-list-item">
        <el-container>
          <el-main>
            <el-link :underline="false" @click="toDetail(showData[i-1].id)"> {{showData[i-1].stockName}}:{{showData[i-1].head}}</el-link>
          </el-main>
          <el-footer height="auto">
            <el-row class="detailInfo">
              <el-col :span="12">{{showData[i-1].author}} {{showData[i-1].date}}</el-col>
              <el-col :span="12" style="text-align: end;">#{{showData[i-1].tag}} {{showData[i-1].stockName}}</el-col>
            </el-row>
          </el-footer>
        </el-container>
      </li>
      <div class="tips">
        <p v-if="loading">加载中...</p>
        <p v-if="noMore">没有更多了</p>
      </div>
    </ul>
    <!-- <el-dialog :visible.sync="dialogVisible" width="925px" top="50px"> -->
    <el-dialog :visible.sync="dialogVisible" width="925px" top="50px" fullscreen>
        <el-container class="dialogContent">
          <el-container>
            <el-main class="dialogTitle">
              <p style="margin: 10px 0px">{{this.detailData.stockName}}：{{this.detailData.head}}</p>
            </el-main>
            <el-footer height="auto">
              <el-row>
                <el-col :span="12">{{this.detailData.author}} {{this.detailData.date}}</el-col>
                <el-col :span="12" style="text-align: end;">#{{this.detailData.tag}} {{this.detailData.stockName}}</el-col>
              </el-row>
            </el-footer>
          </el-container>
          <el-footer height="465px" style="font-size:15px; margin-top: 19px">
            <div style="white-space: pre-wrap; margin: 5px 0 5px 0">{{this.detailData.text}}</div>
          </el-footer>
        </el-container>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'SSInfo',
  props: ['stockId'],
  computed: {
    noMore () {
      return this.count >= this.totalInfoCount
    },
    disabled () {
      return this.loading || this.noMore 
    }
  },
  data () {
    return {
      count: 0,
      dialogVisible: false,
      totalInfoCount: 10,
      loading: false,
      loadingTimer: 0,
      queryInfo: {
        stock_id: '000005.SZ',
        pagenum: 1,
        pagesize: 10
      },
      showData: [
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'1'
        // },{
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'2'
        // },
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'3'
        // },{
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'4'
        // },{
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'5'
        // },
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'6'
        // },
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'7'
        // },
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'8'
        // },
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'9'
        // },
        // {
        //   stock_name:'招商银行',
        //   head:'500亿永续债获董事会通过，发行规模创新高',
        //   date:'2019-06-04 00:00',
        //   author:'财华社',
        //   tag:'股东减持',
        //   id:'10'
        // }
      ],
      detailData: []
    }
  },
  methods: {
    getInfos () {
      this.loading = true
      setTimeout(async () => {
        const { data: res } = await this.$http.get('infos/simple', { params: this.queryInfo })
        if (res.meta.status !== 200) {
            return this.$message.error('数据请求失败')
        }
        this.showData.push(...res.data.detail) 
        this.count += res.data.detail.length
        this.totalInfoCount = res.data.totalpage
        this.queryInfo.pagenum++
        // this.total = res.data.totalpage
        console.log(res)
        // return this.$message.success('数据请求成功')
        this.loading = false
      }, this.loadingTimer)
    },
    async toDetail (id) {
        this.dialogVisible = true
        const { data: res } = await this.$http.get('infos/detail', { params: { infoid:id } } )
        if (res.meta.status !== 200) {
            return this.$message.error('数据请求失败')
        }
        this.detailData = res.data
        console.log(res)
        // return this.$message.success('数据请求成功')
    }
  },
  watch: {
    count (v) {
      if(this.loadingTimer === 0 && v >= 10) {
        this.queryInfo.pagenum = this.count / 2 + 1
        this.queryInfo.pagesize = 2
        this.loadingTimer = 1000
      }
      if (v > this.totalInfoCount - this.queryInfo.pagesize) {
        this.queryInfo.pagesize = this.totalInfoCount - v
        this.queryInfo.pagenum = this.totalInfoCount / this.queryInfo.pagesize + 1
      }
    },
    // stockId (v) {
    //   this.queryInfo.stock_id = v,
    //   this.getInfos()
    // }
  }
}
</script>

<style lang="less" scoped>
.el-main
{
  padding: 0px;
}
.el-footer
{
  padding: 0px;
}
.el-link.el-link--default:hover
{
  color: #ff0000;
}
.dialogTitle
{
  font-size: 19px;
}
.dialogContent
{
  position: relative;
  top:-27px;
  padding: 0px 25px
}
.infinite-list {
  height: 450px;
  padding: 0;
  margin: 3px 0 0 0;
  list-style: none;
}
.infinite-list .infinite-list-item {
  display: flex;
  align-items: center;
  // justify-content: center;
  height: 50px;
  background: #fff9f9;
  margin: 5px;
  color: #6d6d6d;
}
.detailInfo
{
  font-size: 13px;
  color: #b1b1b1;
}
.tips
{
  text-align: center;
  color: #6d6d6d;
  line-height: 3px;
}
::v-deep
{
  .el-dialog__headerbtn:hover .el-dialog__close
  {
    color: #ff4040;
  }
}
.el-dialog {
  overflow: hidden !important;
  display: flex;
  flex-direction: column;
}
.el-dialog__header
{
  text-align: center;
}
.el-dialog__body {
  overflow: scroll;
}
</style>
