<template>
  <div>
    <el-transfer v-model="transferValue" :data="transferData" :titles="transferTitles"
      :button-texts="['删除', '添加']" :left-default-checked="transferDefaultChecked">
      <el-dropdown placement="right" trigger="click" slot="right-footer" 
        style="width:100%" class="slotBtn" @command="toNextSelfStock">
        <el-button style="width:100%" size="medium">切换自选股分组</el-button>
        <el-dropdown-menu>
          <el-dropdown-item :divided="item!=1" v-for="item in 5" :key="item" :command="item">
            自选股-分组{{item}}
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-button type="primary" plain style="width:100%" size="medium"
        slot="left-footer" @click="toNextResultGroup">
        下一组搜索结果
      </el-button>
    </el-transfer>
    <div style="text-align:center">
      <el-button type="danger" style="width: 190px;margin-top:25px" @click="sendInfos">确定</el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AddNewStocks',
  props: ['stocks', 'stockId'],
  mounted() {
    this.queryInfo.user_id = window.sessionStorage.getItem('userId')
    this.addSelfStockInfo.user_id = this.queryInfo.user_id
    this.generateData(this.resultGroup)
  },
  data () {
    return {
      transferData: [],
      transferValue: [],
      transferTitles: ['搜索结果-1','自选股-1'],
      transferDefaultChecked: [],
      resultGroup: 1,
      addSelfStockInfo: {
        user_id: 2,
        stock_code:'',
        group_id:1
      },
      queryInfo: {
        user_id: 2,
        group_id: 1
      }
    }
  },
  methods: {
    async sendInfos () {
      var temp = this.addSelfStockInfo.stock_code
      this.transferValue.forEach((item) => {
        temp += this.transferData[item].stockCode + ','
      })
      temp = temp.substring(0,temp.length - 1)
      this.addSelfStockInfo.stock_code = temp
      console.log(this.addSelfStockInfo)
      const { data: res } = await this.$http.get('users/addStocks', { params: this.addSelfStockInfo })
      if (res.status !== 200) {
        return this.$message.error('自选股添加失败')
      } else {
        this.addSelfStockInfo.stock_code = ''
        return this.$message.success('自选股添加成功')
      }
    },
    async generateData(val) {
      const returnData = []
      const { data: res } = await this.$http.get('selfselection/aside/total', { params: this.queryInfo })
      var selfArray = res.data.details
      var resultStocks = this.stocks.slice((val - 1) * 10,val * 10)
      var selfArrayCode = []
      for (var i = 0; i < selfArray.length; i++) {
        this.transferValue.push(i)
        selfArrayCode.push(selfArray[i].stock_code)
        returnData.push({
          stockCode: selfArray[i].stock_code,
          label: selfArray[i].name,
          key: i
        })
      }
      resultStocks.forEach((item) => {
        if(selfArrayCode.indexOf(item.stock_code) < 0)
          selfArrayCode.push(item.stock_code)
          returnData.push({
            stockCode: item.stock_code,
            label: item.stock_name,
            key: returnData.length
          })
      })
      this.transferData = returnData
      this.transferDefaultChecked = [selfArrayCode.indexOf(resultStocks)]
    },
    toNextResultGroup() {
      this.transferData = []
      this.transferValue = []
      if(++this.resultGroup > (this.stocks.length / 10))
        this.resultGroup = 1
      this.generateData(this.resultGroup)
      this.transferTitles[0] = '搜索结果-' + this.resultGroup
    },
    toNextSelfStock(v) {
      if(v === this.queryInfo.group_id) return
      this.queryInfo.group_id = v
      this.addSelfStockInfo.group_id = v
      this.transferData = []
      this.transferValue = []
      this.generateData(this.resultGroup)
      this.transferTitles[1] = '自选股-' + v
    }
  },
  destroyed() {
  },
  watch: {
    stockId(v) {
      this.transferData.forEach((item, index) => {
        if(v == item.stockCode) 
          this.transferDefaultChecked = [index]
      })
    }
  }
}
</script>

<style lang="less" scoped>
::v-deep
{
  .el-button--primary.is-disabled, .el-button--primary.is-disabled:hover
  {
    background-color: #ffaaaa;
    border: #ffaaaa;
  }
  .el-button--primary
  {
    background-color: #f56c6c;
    border:  #f56c6c;
  }
  .el-button--primary:hover, .el-button--primary:focus
  {
    background-color: #f78989 !important;
    border: #f78989 !important;
  }
  .slotBtn .el-button:hover
  {
    background-color: #fef0f0 !important;
    border: #fccece !important;
    color: #f58d8d !important;
  }
  .slotBtn .el-button:focus
  {
    background-color: #ff9d9d !important;
    border: #ff9d9d !important;
    color: #ffffff !important;
  }
  .el-dropdown-menu__item:focus, .el-dropdown-menu__item:not(.is-disabled):hover
  {
    background-color: #ffecec;
    color: #ff6666;
  }
  .el-transfer-panel .el-transfer-panel__footer
  {
    height: auto;
  }
  .el-input__inner:focus
  {
    border-color: #ff8181 !important;
  }
  .el-input__inner:hover
  {
    border-color: #CCC0C0 !important;
  }
  .el-input__inner
  {
    border-color: #E6DCDC !important;
  }
  .el-button--primary
  {
    background-color: #f56c6c;
    border:  #f56c6c;
  }
  .el-button--primary:hover, .el-button--primary:focus
  {
    background-color: #f78989 !important;
    border: #f78989 !important;
  }
}

</style>

<style>
.el-transfer-panel__filter
{
  margin: 0px !important;
  padding: 5px 3px;
}
.el-checkbox__inner:hover, .el-checkbox__input.is-focus .el-checkbox__inner
{
  border-color: #ffa2a2 !important;
}
.el-checkbox__input.is-checked .el-checkbox__inner, .el-checkbox__input.is-indeterminate .el-checkbox__inner
{
  border-color: #ff8181 !important;
  background-color: #ff8181 !important;
}
.el-transfer-panel__item:hover, .el-checkbox-group .el-checkbox__input.is-checked + .el-checkbox__label
{
  color: #ff8181 !important;
}
.el-transfer-panel .el-transfer-panel__header
{
  background-color: #FAF5F5  !important;
  border-bottom: 1px solid #F5EBEB  !important;
}
.el-transfer-panel
{
  border: 1px solid #f5ebeb !important;
}
.el-button--primary.is-disabled, .el-button--primary.is-disabled:hover
{
  background-color: #ffaaaa;
  border: #ffaaaa;
}
.el-button--primary.is-plain
{
  background-color: #ffffff  !important;
  border: 1px solid #d6d7da  !important;
  color: rgb(112, 112, 112)  !important;
}
.el-button--primary.is-plain:hover
{
  background-color: #fef0f0  !important;
  color: #f58d8d  !important;
}
.el-button--primary.is-plain:focus
{
  background-color: #fef0f0  !important;
  color: #f58d8d  !important;
}
</style>
