<template>
  <div>
    <el-row class="gutter">
      <el-col :span="8" v-for="item in 3" :key="item">
        <div class="indexs">
          <p style="font-size:21px">纳斯达克</p>
          <span style="font-size:27px;">3155.89</span>
          <span>+9.6</span>
          <span>+0.30%</span>
        </div>
        <div style="height:155px">
          <ve-line :data="chartData" :settings="chartSettings" :extend="chartExtend"
          :grid="grid" :colors="colors" :legend-visible="false"
          :judge-width="true" :judge-height="true" ref="veline">
          </ve-line>
        </div>
      </el-col>
    </el-row>
    <div class="container">
      <el-radio-group v-model="queryInfo.tablename" fill="#f04844" @change="radioGroupChange">
        <el-radio-button label="港股通"></el-radio-button>
        <el-radio-button label="沪股通"></el-radio-button>
        <el-radio-button label="深股通"></el-radio-button>
        <el-radio-button label="AH股"></el-radio-button>
      </el-radio-group>
      <el-table :data="tableData" border height="443"
        :cell-style="()=>{return 'text-align: center'}"
        :header-cell-style="()=>{return 'background:#fef1f0;text-align: center'}">
        <el-table-column type="index" label="#" width="39px" fixed="left">
        </el-table-column>
        <el-table-column prop="name" label="名称" fixed="left">
        </el-table-column>
        <el-table-column prop="last" label="最新">
        </el-table-column>
        <el-table-column prop="uprange" label="涨幅">
        </el-table-column>
        <el-table-column prop="charge" label="涨跌">
        </el-table-column>
        <el-table-column prop="prevclose" label="昨收">
        </el-table-column>
        <el-table-column prop="volume" label="成交量">
        </el-table-column>
        <el-table-column prop="turnover" label="成交额">
        </el-table-column>
        <el-table-column prop="max" label="最高">
        </el-table-column>
        <el-table-column prop="min" label="最低">
        </el-table-column>
      </el-table>
      <!-- @size-change="handleSizeChange" @current-change="handleCurrentChange" -->
      <el-pagination :current-page="this.queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10, 20, 100, 200]"
        :page-size="queryInfo.pagesize"
        :total="this.total" background
        layout="total, sizes, prev, pager, next, jumper"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange">
      </el-pagination>
      <!-- <p v-for="i in 15" :key="i">1</p> -->
    </div>
  </div>
</template>

<script>
// import echarts from 'echarts'
export default {
  created () {
    this.getTableData()
  },
  data () {
    this.chartExtend = {
      'xAxis.0.axisLabel.rotate': 45,
      'xAxis.0.axisLabel.color': '#797979',
      'xAxis.0.axisLabel.fontSize': '0',
      'yAxis.0.axisLabel.fontSize': '12',
      'yAxis.0.axisLabel.color': '#797979'
    }
    this.grid = { // 整个绘制区域
      show: true,
      top: 21,
      left: 21,
      // backgroundColor: 'rgba(64,64,64,0.5)',
      width: 250,
      height: 125
    }
    this.chartSettings = {
      stack: { 用户: ['访问用户', '下单用户'] },
      min: [2610, 2800],
      area: true,
      areaStyle: {
        color: {
          type: 'linear',
          x: 0,
          y: 0,
          x2: 0,
          y2: 1,
          colorStops: [
            {
              offset: 0,
              color: 'rgba(255,30,30,0.3)'
            },
            {
              offset: 1,
              color: 'rgba(255,30,30,0.1)'
            }
          ],
          global: false // 缺省为 false
        },
        lineStyle: {
          color: 'rgba(255,30,30,0.1)'
        }
      }
    }
    return {
      radio: '港股通',
      userid: 0,
      queryInfo: {
        tablename: '港股通',
        pagenum: 1,
        pagesize: 1
      },
      total: 1,
      tableData: [
        {
          // name: 'NULL',
          // last: 0,
          // uprange: 0,
          // charge: 0,
          // prevclose: 0,
          // volume: 0,
          // turnover: 0,
          // max: 0,
          // min: 0,
          // amplitude: 0,
          // accelerate: 0
        }
      ],
      colors: ['#FF9999'],
      chartData: {
        columns: ['日期', '指数'],
        rows: [
          { 日期: '1/1', 指数: '2770' },
          { 日期: '1/2', 指数: '2730' },
          { 日期: '1/3', 指数: '2760' },
          { 日期: '1/4', 指数: '2710' },
          { 日期: '1/5', 指数: '2690' },
          { 日期: '1/6', 指数: '2680' },
          { 日期: '1/7', 指数: '2660' },
          { 日期: '1/8', 指数: '2640' },
          { 日期: '1/9', 指数: '2690' },
          { 日期: '1/10', 指数: '2650' },
          { 日期: '1/11', 指数: '2630' }
        ]
      }
    }
  },
  methods: {
    async getTableData () {
      const { data: res } = await this.$http.get('market/hkstocks', { params: this.queryInfo })
      if (res.meta.status !== 200) {
          return this.$message.error('数据请求失败')
      }
      this.tableData = res.data.details
      this.total = res.data.totalpage
      // console.log('请求参数')
      // console.log(this.queryInfo)
      // console.log('数据')
      console.log(res)
      // return this.$message.success('数据请求成功')
    },
    handleSizeChange (val) {
        this.queryInfo.pagesize = val
        this.getTableData()
        // console.log(`每页 ${val} 条`)
    },
    handleCurrentChange (val) {
        this.queryInfo.pagenum = val
        this.getTableData()
        // console.log(`当前页: ${val}`)
    },
    radioGroupChange (v) {
      this.getTableData()
    }
  }
}
</script>

<style lang="less" scoped>
.container
{
  background: #FFFFFF;
  padding: 0 15px 0 15px;
}
.titleBar
{
  width: 750px;
  padding: 7px 0 7px 10px;
}
.gutter
{
  border-bottom: 10px solid #eeeeee !important;
}
.indexs
{
  margin-left: 21px;
  line-height:3px
}
.indexs span
{
  margin-right: 21px;
  color: red;
}
.el-radio-group
{
  margin: 5px 0px 5px 0px;
}
.el-radio-button
{
  margin-right: 25px;
}
.el-pagination
{
  margin: 10px 0 10px 0;
}
::v-deep
{
  .el-pagination.is-background .el-pager li:not(.disabled).active
  {
    background-color: #f14747;   // 进行修改选中项背景和字体
    color: #EEEEEE;
  }
  .el-pagination.is-background .el-pager li:not(.active):hover
  {
    color:#ff4040;
  }
  .el-radio-button__inner:hover
  {
    color: #f56c6c;
  }
  .el-radio-button__inner
  {
    border-radius:4px 4px 4px 4px !important;
    border:1px solid #fccece;
    background: #FFFFFF;
    color: #f56c6c;
    width: 100px;
  }
}
</style>
