<template>
    <div>
        <div class="dialog_title">牪牪牛</div>
        <el-divider>使用第三方账户注册</el-divider>
        <el-row :gutter="25" type="flex" justify="center">
        <el-col :span="8" class="col-image">
            <el-image :src="require('../../assets/icon/wx.png')" fit="cover"></el-image>
        </el-col>
        <el-col :span="8" class="col-image">
            <el-image :src="require('../../assets/icon/qq.png')" fit="cover"></el-image>
        </el-col>
        </el-row>
        <!-- 手机号 -->
        <el-divider>使用手机号注册</el-divider>
        <el-form ref="registerFormRef" :model="formData"
            :rules="formDataRules" label-width="0px">
        <el-form-item prop="phone">
            <el-input v-model="formData.phone"
                prefix-icon="el-icon-mobile-phone" placeholder="手机号">
            </el-input>
        </el-form-item>
        <!-- 验证码 -->
        <el-form-item>
            <el-row :gutter="10">
            <el-col :span="16">
                <el-input v-model="formData.verifycode"
                    prefix-icon="el-icon-message" placeholder="验证码">
                </el-input>
            </el-col>
            <el-col :span="8">
                <el-button type="info" plain>发送验证码</el-button>
            </el-col>
            </el-row>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
            <el-input  v-model="formData.password" type="password"
                prefix-icon="el-icon-lock" placeholder="密码" show-password>
            </el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" class="login_btn" @click="submitForm()">
                注册
            </el-button>
        </el-form-item>
        </el-form>
        <el-row type="flex" justify="space-between" class="jump_link">
            <el-col :span="12"><el-link type="primary" @click="toRetrievePwd()">忘记密码>>></el-link></el-col>
            <el-col :span="8"><el-link type="primary" @click="toLogin()">去登陆>>></el-link></el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    data () {
        var checkPhone = (rule, value, callback) => {
            if (!value) {
                return callback(new Error('手机号不能为空'))
            } else {
                const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
                if (reg.test(value)) {
                    callback()
                } else {
                    return callback(new Error('请输入正确的手机号'))
                }
            }
        }
        return {
            formData: {
                phone: '',
                verifycode: '',
                password: ''
            },
            formDataRules: {
                phone: [
                    { required: true, message: '请输入手机号', trigger: 'blur' },
                    { validator: checkPhone, trigger: ['blur', 'change'] }
                ],
                password: [
                    { required: true, message: '请输入密码', trigger: 'blur' },
                    { min: 6, max: 11, message: '长度在 6 到 11 个字符', trigger: 'blur' }
                ]
            }
        }
    },
    methods: {
        toLogin () {
            this.$emit('getMessage', '110px')
            this.$router.push('/login')
        },
        toRetrievePwd () {
            this.$emit('getMessage', '185px')
            this.$router.push('/retrievePwd')
        },
        submitForm () {
            this.$refs.registerFormRef.validate(async valid => {
                if (!valid) {
                    return
                }
                const { data: res } = await this.$http.post('register/phone', this.formData)
                if (res.meta.status !== 200) {
                    return this.$message.error('注册失败！')
                }
                this.$message.success('注册成功！')
                console.log(res)
                // window.sessionStorage.setItem('token', res.data.token)
                this.toLogin()
            })
        }
    }
}
</script>

<style lang="less" scoped>
.dialog_title
{
  font-size:37px;
  text-align: center;
}
.el-divider
{
//   width: 350px;
  width: 100%;
  margin-top:45px;
  // margin-left:105px;
}
.el-divider__text
{
  color: #7e7e7e;
}
.el-form
{
  // width: 350px;
  width: 100%;
  // margin-left: 105px;
}
.login_btn
{
  // width: 350px;
  width: 100%;
  font-size: 20px;
}
.jump_link
{
  // width: 375px;
  width: 100%;
  // margin-left: 110px;
}
.col-image {
  text-align: center;
  margin-top: 12px;
}
</style>
