<template>
  <div class="container-search">
    <div class="title0"><i class="el-icon-s-marketing"></i>牪牪牛</div>
    <div style="text-align:center;">
      <el-autocomplete style="width: 90%;" :trigger-on-focus="false"
        v-model="searchSelected" placement="bottom-end"
        :fetch-suggestions="querySearch" value-key="stock_name"
        placeholder="请输入股票代码/名称/首字母" @select="handleInputSelect"
        @keyup.enter.native="submitForm" class="searchBar">
        <template slot-scope="{ item }">
          <el-row>
            <el-col :span="12" class="autocompleteCode">{{ item.stock_name }}</el-col>
            <el-col :span="11" class="autocompleteName">{{ item.stock_code }}</el-col>
          </el-row>
        </template>
      </el-autocomplete>
      <el-button type="danger" @click="submitForm">搜索</el-button>
    </div>
  </div>
</template>

<script>
import pinyinMatch from 'pinyin-match'

export default {
  name: 'SimpleSearch',
  data() {
    return {
      searchSelected: '',
      queryResults: [],
      rawSearchData: []
    }
  },
  mounted() {
    this.loadAll()
    // console.log(pinyinMatch.match('世纪星源','sjxy'))
    // console.log(pinyin.getFullChars('世纪星源'))
  },
  methods: {
    async loadAll() {
      const { data: res } = await this.$http.get('home/searchData')
      this.rawSearchData = res.data.details
      console.log(this.rawSearchData)
    },
    handleInputSelect(item) {
      this.searchSelected = item.stock_name
      window.sessionStorage.setItem('searchResult',JSON.stringify(item))
      this.$router.push({
        path: '/pickStocks',
        query: {
          results: true
        }
      })
    },
    querySearch(queryString, cb) {
      var results = queryString ? this.rawSearchData.filter(this.createFilter(queryString)) : this.rawSearchData
      // 调用 callback 返回建议列表的数据
      console.log(results)
      results.length > 50 ? cb(results.slice(0,30)) : cb(results)
      this.queryResults = results.slice(0,30)
    },
    createFilter(queryString) {
      if(isNaN(Number(queryString))) {
        return (rest) => {
          return (rest.stock_name.toLowerCase().indexOf(queryString.toLowerCase()) >= 0) 
            || this.pinyinJudge(rest, queryString)
        }
      } else {
        return (rest) => {
          return (rest.stock_code.toLowerCase().indexOf(queryString.toLowerCase()) >= 0)
        }
      }
    },
    pinyinJudge(rest,queryString) {
      var pinyinJudgeResult = pinyinMatch.match(rest.stock_name,queryString)
      return pinyinJudgeResult[0] === 0
    },
    submitForm() {
      if(this.searchSelected != '') {
        window.sessionStorage.setItem('searchResult',JSON.stringify(this.queryResults))
        this.$router.push({
          path: '/pickStocks',
          query: {
            results: true
          }
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
.container-search {
  font-size: 16px;
  width: 100%;
  // height: 240px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.title0
{
  // margin-top: 85px;
  font-size: 45px;
  text-align: center;
  line-height: 75px;
  letter-spacing:7px;
  color: #f56c6c;
}
.searchBar {
  margin: 20px 0 60px;
}
.el-autocomplete
{
  width: 650px;
}
.el-autocomplete-suggestion
{
  width: 650px;
}
.autocompleteCode
{
  text-overflow: ellipsis;
  overflow: hidden;
}
.el-button
{
  border-radius: 0 4px 4px 0;
}
.autocompleteName
{
  font-size: 15px;
  color: #b4b4b4;
  text-align: end;
}
::v-deep
{
  .el-input__inner
  {
    border-color: #bdafaf;
    border-radius: 4px 0 0 4px;
  }
  .el-input__inner:hover
  {
    border-color: #8f8484;
  }
}

</style>
