import Vue from 'vue'
import VueRouter from 'vue-router'
import Welcome from '../components/Welcome/Welcome.vue'
import Home from '../components/Home/Home.vue'
import Register from '../components/Welcome/Register.vue'
import Login from '../components/Welcome/Login.vue'
import RetrievePwd from '../components/Welcome/RetrievePwd.vue'
import RestPwd from '../components/Welcome/RestPwd.vue'
import Market from '../components/Home/Market/Market.vue'
import Block from '../components/Home/Market/Block.vue'
import BlockDetail from '../components/Home/Market/BlockDetail.vue'
import Indexs from '../components/Home/Market/Indexs.vue'
import SelfSelection from '../components/Home/SelfSelection/SelfSelection.vue'
import InfoList from '../components/Home/Infos/InfoList.vue'
import DetailInfo from '../components/Home/Infos/DetailInfo.vue'
import Application from '../components/Home/Application/Application.vue'
import SLConnect from '../components/Home/Market/SLConnect'
import SSSMarket from '../components/Home/Market/SSSMarket'
import HKStocks from '../components/Home/Market/HKStocks'
import USStocks from '../components/Home/Market/USStocks'
import PickStocks from '../components/Home/PickStocks/PickStocks'
import PersonalCenter from '../components/Home/PersonalCenter/PersonalCenter'
import Test from '../components/test.vue'
import Test2 from '../components/test2.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/welcome'
  },
  {
    path: '/welcome',
    name: 'welcome',
    component: Welcome,
    children: [
      {
        path: '/register',
        name: 'register',
        component: Register
      },
      {
        path: '/login',
        name: 'login',
        component: Login
      },
      {
        path: '/retrievePwd',
        name: 'retrievePwd',
        component: RetrievePwd
      },
      {
        path: '/restPwd',
        name: 'restPwd',
        component: RestPwd
      }
    ]
  },
  {
    path: '/home',
    name: 'home',
    redirect: '/market',
    component: Home,
    children: [
      {
          path: '/market',
          name: 'market',
          redirect: '/sssmarket',
          component: Market,
          children: [
            {
                path: '/block',
                name: 'block',
                component: Block
            },
            {
              path: '/indexs',
              name: 'indexs',
              component: Indexs
            },
            {
              path: '/slconnect',
              name: 'slconnect',
              component: SLConnect
            },
            {
              path: '/sssmarket',
              name: 'sssmarket',
              component: SSSMarket
            },
            {
              path: '/hkstocks',
              name: 'hkstocks',
              component: HKStocks
            },
            {
              path: '/usstocks',
              name: 'usstocks',
              component: USStocks
            }
          ]
      },
      {
        path: '/blockDetail',
        name: 'blockDetail',
        component: BlockDetail
      },
      {
          path: '/selfSelection',
          name: 'selfSelection',
          component: SelfSelection
      },
      {
        path: '/pickStocks',
        name: 'pickStocks',
        component: PickStocks
      },
      {
        path: '/infoList',
        name: 'infoList',
        component: InfoList
      },
      {
        path: '/detailInfo',
        name: 'detailInfo',
        component: DetailInfo
      },
      {
        path: '/application',
        name: 'application',
        component: Application
      },
      {
        path: '/me',
        name: 'me',
        component: PersonalCenter
      }
    ]
  },
  {
    path: '/test',
    name: 'test',
    component: Test
  },
  {
    path: '/test2',
    name: 'test2',
    component: Test2
  }
]

//获取原型对象上的push函数
const originalPush = VueRouter.prototype.push
//修改原型对象中的push方法
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const router = new VueRouter({
  routes
})

export default router
