<template>
  <div>
    <div class="dialog_title">牪牪牛</div>
    <el-divider>重置密码</el-divider>
    <el-form ref="restPwdFormRef" :model="formData" :rules="formDataRules"
      label-width="0px">
      <!-- 第一次密码 -->
      <el-form-item prop="password">
        <el-input  v-model="formData.password" type="password"
          prefix-icon="el-icon-unlock" show-password placeholder="密码">
        </el-input>
      </el-form-item>
      <!-- 第二次密码 -->
      <el-form-item prop="password">
        <el-input  v-model="formData.repassword" type="password"
          prefix-icon="el-icon-lock" show-password placeholder="确认密码">
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" class="login_btn" @click="submitForm()">
          确定
        </el-button>
      </el-form-item>
    </el-form>
    <el-row type="flex" justify="space-between" class="jump_link">
      <el-col :span="12">
        <el-link type="primary" @click="toLogin()">又想起来了>>></el-link>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
    return {
      userid: 3,
      formData: {
        password: '',
        repassword: ''
      },
      formDataRules: {
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 11, message: '长度在 6 到 11 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    toLogin () {
      this.$emit('getMessage', '110px')
      this.$router.push('/login')
    },
    submitForm () {
      this.$refs.restPwdFormRef.validate(async valid => {
        if (!valid) {
            return
        }
        const { data: res } = await this.$http.put('reset/repwd', { password: this.formData.password, id: this.userid })
        if (res.meta.status !== 200) {
            return this.$message.error('修改失败！')
        }
        this.$message.success('修改成功！')
        console.log(res)
        // window.sessionStorage.setItem('token', res.data.token)
        this.toLogin()
      })
    }
  }
}
</script>

<style lang="less" scoped>
.dialog_title
{
  font-size:37px;
  text-align: center;
}
.el-divider
{
  // width: 350px;
  width: 100%;
  margin-top:45px;
  // margin-left:105px;
}
