<template>
  <div>
    <el-timeline>
      <el-timeline-item :timestamp="item" placement="top"
        v-for="(item, index) in timeLineInfo" :key="index">
        <!-- <el-link style="font-size:17px" @click.native="extend(index)">
          展开<i class="el-icon-arrow-down"></i>
        </el-link> -->
        <el-collapse v-model="activeName[index]" @change="collapseChanged" accordion>
          <el-collapse-item :name="item" title="展开">
            <el-table :data="detailTableInfo[index]" stripe :ref="{ref: item + 'ref'}.ref"
              :span-method="objectSpanMethod" style="width: 100%" border
              :cell-style="()=>{return 'background-color:#F9F9F9;text-align: center'}"
              :header-cell-style="()=>{return 'background-color:#F1F1F1;text-align: center'}">
              <el-table-column prop="sort" width="30px">
              </el-table-column>
              <el-table-column label="业务名称" prop="business">
              </el-table-column>
              <el-table-column label="收入(万元)" prop="earning">
              </el-table-column>
              <el-table-column label="收入占比(%)" prop="proportion">
              </el-table-column>
              <el-table-column label="毛利率(%)" prop="gross">
              </el-table-column>
            </el-table>
          </el-collapse-item>
        </el-collapse>
      </el-timeline-item>
    </el-timeline>
  </div>
</template>

<script>
export default {
  name: 'SSComposition',
  props: ['stockId'],
  created () {
  },
  data () {
    return {
      activeName: [],
      MergeCellsCounts: [0, 0, 0],
      openOrClose: [false,false,false,false],
      timeLineInfo: ['2019末期','2019中期','2018末期','2018中期'],

      detailTableInfo:[
        [
          {
            business: '',
            earning: '',
            proportion: '',
            gross: '',
            sort: ''
          }
        ],[],[],[]
      ]
    }
  },
  methods: {
    async getInfos (id, period) {
      const { data: res } = await this.$http.get('selfselection/bottom/composition', { params: {stockid: id, period: period} })
      if (res.meta.status !== 200) {
        return this.$message.error('数据请求失败')
      }
      var temp = []
      var target = ['产品', '行业', '地区']
      for (let index = 0; index < 3; index++) {
        res.data.detail.forEach(element => {
          if (element.sort === target[index]) {
            temp.push(element)
            this.MergeCellsCounts[index]++
          }
        })
      }
      this.$set(this.detailTableInfo, this.timeLineInfo.indexOf(period), temp)
      console.log(this.detailTableInfo)
      // this.total = res.data.totalpage
      console.log(res)
      // return this.$message.success('数据请求成功')
    },
    objectSpanMethod ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0) {
        if (rowIndex === 0) {
          return { rowspan: this.MergeCellsCounts[0], colspan: 1 }
        } else if (rowIndex === this.MergeCellsCounts[0]) {
          return { rowspan: this.MergeCellsCounts[1], colspan: 1 }
        } else if (rowIndex === this.MergeCellsCounts[0] + this.MergeCellsCounts[1]) {
          return { rowspan: this.MergeCellsCounts[2], colspan: 1 }
        } else {
          return { rowspan: 0, colspan: 0 }
        }
      }
    },
    collapseChanged (activeNames) {
      var indexOfActiveItem = this.timeLineInfo.indexOf(activeNames)
      if (indexOfActiveItem >= 0) {
        if (!this.openOrClose[indexOfActiveItem]) {
          this.getInfos(this.stockId ,activeNames)
          this.openOrClose[indexOfActiveItem] = !this.openOrClose[indexOfActiveItem]
        } 
      } else {
        this.openOrClose = [false,false,false,false]
      }
    }
  }
}
</script>

<style lang="less" scoped>
.el-timeline
{
  padding: 21px 0 0 15px;
}
.el-collapse
{
  width: 95%;
}
::v-deep
{
  .el-timeline-item__timestamp
  {
    font-size: 17px !important;
    color: #776b6e;
  }
  .el-timeline-item__wrapper
  {
    top: -7px  !important;
  }
  .el-collapse-item__content
  {
    padding-bottom: 0px;
  }
  .el-collapse-item__header
  {
    height: 35px;
    padding-left: 92%;
    color: #ab9399;
  }
}
</style>
