    getInfos () {
      this.loading = true
      setTimeout(async () => {
        const { data: res } = await this.$http.get('selfselection/bottom/events', { params: this.queryInfo })
        if (res.meta.status !== 200) {
            return this.$message.error('数据请求失败')
        }
        this.showData.push(...res.data.events) 
        console.log(this.showData)
        this.count += res.data.events.length
        this.totalInfoCount = res.data.totalpage
        this.queryInfo.pagenum++
        // this.total = res.data.totalpage
        console.log(res)
        // return this.$message.success('数据请求成功')
        this.loading = false
      }, this.loadingTimer)
    }
  },
  watch: {
    count (v) {
      if(this.loadingTimer === 0 && v >= 10) {
        this.queryInfo.pagenum = this.count / 2 + 1
        this.queryInfo.pagesize = 2
        this.loadingTimer = 1000
      }
      if (v > this.totalInfoCount - this.queryInfo.pagesize) {
        this.queryInfo.pagesize = this.totalInfoCount - v
        this.queryInfo.pagenum = this.totalInfoCount / this.queryInfo.pagesize + 1
