<template>
  <div>
    <div class="dialog_title">牪牪牛</div>
    <el-divider>找回密码</el-divider>
    <el-form ref="loginFormRef" :model="formData" :rules="formDataRules"
      label-width="0px" class="login_form">
      <!-- 手机号 -->
      <el-form-item prop="phone">
        <el-input v-model="formData.phone" prefix-icon="el-icon-mobile-phone"
          placeholder="手机号">
        </el-input>
      </el-form-item>
      <!-- 验证码 -->
      <el-form-item>
        <el-row :gutter="10">
          <el-col :span="16">
            <el-input v-model="formData.verifycode"
              prefix-icon="el-icon-message"  placeholder="验证码">
            </el-input>
          </el-col>
          <el-col :span="8">
            <el-button type="info" plain>发送验证码</el-button>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" class="login_btn" @click="submitForm()">
          下一步
        </el-button>
      </el-form-item>
    </el-form>
    <el-row type="flex" justify="space-between" class="jump_link">
      <el-col :span="12">
        <el-link type="primary" @click="toLogin()">又想起来了>>></el-link>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data () {
    var checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('手机号不能为空'))
      } else {
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/
        if (reg.test(value)) {
          callback()
        } else {
          return callback(new Error('请输入正确的手机号'))
        }
      }
    }
    return {
      formData: {
        phone: '',
        verifycode: ''
      },
      formDataRules: {
        phone: [
          { required: true, message: '请输入手机号', trigger: 'blur' },
          { validator: checkPhone, trigger: ['blur', 'change'] }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 6, max: 11, message: '长度在 6 到 11 个字符', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    toLogin () {
      this.$emit('getMessage', '110px')
      this.$router.push('/login')
    },
    submitForm () {
      this.$router.push('/restPwd')
    }
  }
}
</script>

<style lang="less" scoped>
.dialog_title
{
  font-size:37px;
  text-align: center;
}
.el-divider
{
  // width: 350px;
  width: 100%;
  margin-top:45px;
