<template>
  <div class="Echarts">
    <div id="timesharingchart" ref="kChart" style="width: 100%;height:500px;"></div>
  </div>
</template>

<script>
var upColor = '#ec0000';
var downColor = '#00da3c';
export default {
  name: 'TimeSharing',
  props: ['toReSize', 'stockId'],
  data() {
    this.myChart = ''
    this.lineColor = ['#c23531','#2f4554']
    return {
      hours: 9,
      minutes: 0, 
      loopTimes: 0, 
      volumeTimes: 18,
      data: {
        categoryData: ["9:00","9:01","9:02","9:03","9:04","9:05","9:06",
          "9:07","9:08","9:09","9:10","9:11","9:12","9:13","9:14","9:15","9:16","9:17"],
        series0: ["0","12","13","14","15","14","13","12","11",
          "-11","-12","-13","-14","-15","-14","-13","-12","-11"],
        series1: ["0","13","15","16","19","21","17","14","10","8","5",
          "7","7","3","1","4","8","13"],
        series2: ["0","20","21","23","27","26","22","20","15","14","10",
          "9","9","5","3","6","10","15"],
        series3: [[0,0,1],[1,11,1],[2,13,1],[3,11,-1],[4,21,-1],[5,14,-1],[6,15,1],[7,13,1],[8,10,1],[9,12,1],[10,11,1],
          [11,21,-1],[12,13,-1],[13,14,-1],[14,19,1],[15,21,1],[16,13,-1],[17,12,1]]
      },
      option: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            crossStyle: {
              color: '#999'
            }
          }
        },
        grid: [
          {
            left: '13.5%',
            right: '7.5%',
            height: '50%',
            height: '50%'
          },
          {
            left: '13.5%',
            right: '7.5%',
            top: '67%',
            height: '25%'
          }
        ],
        xAxis: [
          {
            type: 'category',
            gridIndex: 0,
            data: [],
            scale: true,
            boundaryGap: false,
            axisLine: {onZero: false},
            axisTick: {show: false},
            splitLine: {show: false},
            axisLabel: {show: false},
            splitNumber: 20,
            min: 'dataMin',
            max: 'dataMax'
          },
          {
            type: 'category',
            gridIndex: 1,
            data: [],
            scale: true,
            boundaryGap: false,
            axisLine: {onZero: false},
            axisTick: {show: false},
            // splitLine: {show: false},
            // axisLabel: {show: false},
            splitNumber: 20,
            min: 'dataMin',
            max: 'dataMax'
          }
        ],
        yAxis: [
          {
            scale: true,
            gridIndex: 0,
          },
          {
            scale: true,
            gridIndex: 1,
            splitNumber: 2,
          }
        ],
        series: [
          {
            type: 'bar',
            data: [],
            itemStyle: {
              color: param => {
                return (param.data < 0 ? downColor : upColor);
              }
            }
          },
          {
            type: 'line',
            yAxisIndex: 0,
            smooth: true,
            symbol: 'none',
            data: [],
            lineStyle: {
              width: 2,
              color: this.lineColor[0],
              opacity: 0.5
            }
          },
          {
            type: 'line',
            yAxisIndex: 0,
            smooth: true,
            symbol: 'none',
            data: [],
            lineStyle: {
              width: 2,
              color: this.lineColor[1],
              opacity: 0.5
            }
          },
          {
            type: 'bar',
            data: [],
            xAxisIndex: 1,
            yAxisIndex: 1,
            itemStyle: {
              color: param => {
                return (param.data[2] < 0 ? '#59deff' : '#b368ff');
              }
            }
          },
        ],
      }
    }
  },
  mounted() {
    this.myChart = this.$echarts.init(document.getElementById('timesharingchart'))
    this.myChart.setOption(this.option)
    window.chatTimer = setInterval(()=>{
      if(this.minutes === 0 && this.hours === 9)
      {
        this.option.xAxis[0].data = this.data.categoryData
        this.option.xAxis[1].data = this.data.categoryData
        this.option.series[0].data = this.data.series0
        this.option.series[1].data = this.data.series1
        this.option.series[2].data = this.data.series2
        this.option.series[3].data = this.data.series3
        this.myChart.setOption(this.option);
        this.minutes = 18;
      } else {
        this.loopTimes > 17 ? this.loopTimes = 0 : ''
        if(this.minutes === 60){
          this.minutes = 0
          this.hours++
        }
        if(this.minutes < 10)
          this.option.xAxis[1].data.push(this.hours+':0'+this.minutes++)
        else
          this.option.xAxis[1].data.push(this.hours+':'+this.minutes++)
        this.option.xAxis[0].data = this.option.xAxis[1].data
        this.option.series[0].data.push(this.data.series0[this.loopTimes])
        this.option.series[1].data.push(this.data.series1[this.loopTimes])
        this.option.series[2].data.push(this.data.series2[this.loopTimes++])
        this.option.series[3].data.push([this.volumeTimes++,Math.ceil(Math.random()*20),Math.random()>0.5 ? 1 : -1])
        this.myChart.setOption(this.option);
      }
    }, 1000);
  },
  methods: {
  },
  beforeDestroy() {
    clearInterval(window.chatTimer);
  },
  watch: {
    toReSize() {
      this.myChart.resize()
    },
    stockId(v) {
      // this.QueryInfo.stock_code = v
      // this.getData()
    }
  }
}
</script>

<style lang="less" scoped>
</style>
